import * as req from '@/public/request.js'
import api from '@/config/api.js'
import types from '../types.js'

const state = {
     asideWidth:"220",
     messageStatus:"",
     group:[]
}
const getters = {
     getDataGroup(state){ 
       return JSON.parse(sessionStorage.getItem("group"));
     },
     getAsideWidth(state){
       return state.asideWidth;
     }
}
const mutations = {
   [types.GET_GROUP](state,data){
      //组
   	  state.group = data;
   },
   [types.GET_WIDTH](state,data){
      //首次打开系统home，右侧aside宽度
      state.asideWidth === '220' ? state.asideWidth = '150' : state.asideWidth = '220';
   }
}
const actions = {
   getGroup({commit,state},obj){
       req.get(api.GET_GROUP,{groups:1}).then(res => {
           console.log("group登录",res);
           commit(types.GET_GROUP,res);
           //存储至sessionStorage
           sessionStorage.setItem("group",JSON.stringify(res));
       })
   },
   getWidth({commit,state}){
       commit(types.GET_WIDTH);
   }
}
export default{
	state,
	getters,
	mutations,
	actions
}


