import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login/index.vue'
import Manage from '../components/manage/manage.vue'
import Home from '../components/home/home.vue'
import Apidessc from '../components/home/apidessc.vue'
import Datamange from '../components/home/main.vue'
import Message from '../components/home/message.vue'
import Userinfo from '../components/home/userinfo.vue'
import Modify from '../components/home/modify.vue'
import Project from '../components/home/project.vue'
import Website from '../components/home/website.vue'
import Wchat from '../components/home/wchat.vue'
import Visitor from '../components/home/visitor.vue'

Vue.use(Router)

const originalPush = Router.prototype.push           //控制台router报错，但不影响功能
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login            //login登录
    },
    {
      path: '/manage',
      name: 'manage',
      component: Manage           //manage分组选择
    },
    {
      path: '/home',              
      name: 'home',        
      component: Home,             //home个人中心
      children:[
         {
         	path: 'apidessc',      //api功能说明
         	name: 'apidessc',
         	component: Apidessc
         },
         {
         	path: 'datamange',      //数据概览
         	name: 'datamange',
         	component: Datamange
         },
         {
          path: 'website',      //网站管理
          name: 'website',
          component: Website
         },
         {
         	path: 'message',      //消息中心
         	name: 'message',
         	component: Message
         },
         {
          path: 'project',      //项目管理
          name: 'project',
          component: Project
         },
         {
          path: 'visitor',      //数据实时访客
          name: 'visitor',
          component: Visitor
         },
         {
          path: 'wchat',      //微信管理
          name: 'wchat',
          component: Wchat
         },
         {
          path: 'user',
          name: 'user',
          component: Userinfo   //基本资料
         },
         {
          path: 'modify',
          name: 'modify',
          component: Modify   //修改密码
         }
      ]
    },
    {
      path: "*",
      redirect: "/login"         //router 重定向 
    }
  ]
})



