<template>
    <div id="aside">
	    <el-menu
	      default-active="2"
	      class="el-menu-vertical-demo"
	      @open="handleOpen"
	      @close="handleClose"
	      @select="openSelect"
	      background-color="#545c64"
	      text-color="#fff"
	      active-text-color="#ffd04b">
	      <el-submenu index="1" collapse="true">
	          <template slot="title">
                  <i class="el-icon-s-home"></i>
                  <span>主页</span>
	          </template>
	          <el-menu-item index="1-1">API/功能说明</el-menu-item>
	          <el-menu-item index="1-2">数据概览</el-menu-item>
	      </el-submenu>
	       <el-submenu index="2">
	          <template slot="title">
                  <i class="el-icon-s-platform"></i>
                  <span>站点</span>
	          </template>
	          <el-menu-item index="2-1">网站管理</el-menu-item>
	      </el-submenu>
	       <el-submenu index="3">
	          <template slot="title">
                  <i class="el-icon-s-comment"></i>
                  <span>微信</span>
	          </template>
	          <el-menu-item index="3-1">微信管理</el-menu-item>
	      </el-submenu>
	       <el-submenu index="4">
	          <template slot="title">
                  <i class="el-icon-s-data"></i>
                  <span>数据</span>
	          </template>
	          <el-menu-item index="4-1">实时数据访客</el-menu-item>
	      </el-submenu>
	       <el-submenu index="5">
	          <template slot="title">
                  <i class="el-icon-s-promotion"></i>
                  <span>项目</span>
	          </template>
	          <el-menu-item index="5-1">项目管理</el-menu-item>
	      </el-submenu>
	      <el-submenu index="6">
	          <template slot="title">
                  <i class="el-icon-s-tools"></i>
                  <span>设置</span>
	          </template>
	          <el-menu-item index="6-1">基本资料</el-menu-item>
	          <el-menu-item index="6-2">修改密码</el-menu-item>
	      </el-submenu>
	    </el-menu>
    </div>	
</template>
<script>
    import { bus } from '@/public/bus.js'

	export default{
        data(){
        	return{

        	}
        },
        methods: {
          handleOpen(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      handleClose(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      openSelect(index,indexPath){
	      	  if(index == "1-1" && indexPath[0] == "1"){
	      	  	  this.keepRouterPath("/home/apidessc","vue-apidessc");
	      	  }
	      	  if(index == "1-2" && indexPath[0] == "1"){
	      	  	  this.keepRouterPath("/home/datamange","vue-main");
	      	  }
	      	  if(index == "2-1" && indexPath[0] == "2"){
	      	  	  this.keepRouterPath("/home/website","vue-website");
	      	  }
	      	  if(index == "3-1" && indexPath[0] == "3"){
	      	  	  this.keepRouterPath("/home/wchat","vue-wchat");
	      	  }
	      	  if(index == "4-1" && indexPath[0] == "4"){
	      	  	  this.keepRouterPath("/home/visitor","vue-visitor");
	      	  }
	      	  if(index == "5-1" && indexPath[0] == "5"){
	      	  	  this.keepRouterPath("/home/project","vue-project");
	      	  }
	      	  if(index == "6-1" && indexPath[0] == "6"){
	      	  	  this.keepRouterPath("/home/user","vue-userinfo");
	      	  }
	      	  if(index == "6-2" && indexPath[0] == "6"){
	      	  	  this.keepRouterPath("/home/modify","vue-modify");
	      	  }
	      },
	      keepRouterPath(path,component){
	      	  this.$router.push(path);
	      	  bus.$emit("v-component",component);
	      }
	    }
	}
</script>
<style scoped lang="less">
    .el-menu{
       width:100%;
    }
</style>




