<template>
	<div id="message">
	    <div class="position">
		 	 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/message','vue-message')">消息中心</el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="box-tab">
		 	  <el-tabs v-model="activeName" @tab-click="handleClick">
			    <el-tab-pane label="全部消息" name="first">
			    	<div class="box-wrap">
                        <el-table :data="messageAll.slice((currentPage-1)*pageSize,currentPage*pageSize)" style="width: 100%" v-loading="loading">
                              <template slot="empty">
						            <p>{{dataText}}</p>
						      </template>
					          <el-table-column prop="id" label="ID" width="180"></el-table-column>
					          <el-table-column prop="content" label="标题内容" width="600"></el-table-column>
					          <el-table-column prop="time" label="时间" width="180"></el-table-column>
				        </el-table>
			    	</div>
			     <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="alllen">
			    </el-pagination>
			    </el-tab-pane>
			    <el-tab-pane label="系统公告" name="second">
			    	 <div class="box-wrap">
                        <el-table :data="messageSestem.slice((currentPage2-1)*pageSize,currentPage2*pageSize)" style="width: 100%">
					          <el-table-column prop="id" label="ID" width="180"></el-table-column>
					          <el-table-column prop="content" label="标题内容" width="600"></el-table-column>
					          <el-table-column prop="time" label="时间" width="180"></el-table-column>
				        </el-table>
			    	</div>
			     <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage2"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="sestemlen">
			    </el-pagination>
			    </el-tab-pane>
			    <el-tab-pane label="更新日志" name="third">
			    	<div class="box-wrap">
                        <el-table :data="messageUpdate.slice((currentPage3-1)*pageSize,currentPage3*pageSize)" style="width: 100%">
					          <el-table-column prop="id" label="ID" width="180"></el-table-column>
					          <el-table-column prop="content" label="标题内容" width="600"></el-table-column>
					          <el-table-column prop="time" label="时间" width="180"></el-table-column>
				        </el-table>
			    	</div>
			     <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage3"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="updatelen">
			    </el-pagination>
			    </el-tab-pane>
			  </el-tabs>
		 </div>
	</div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex'
    import { bus } from '@/public/bus.js'

	export default{
		data() {
	      return {	    	
	        message:{},
	        messageAll:[],      //全部消息
	        messageSestem:[],   //系统公告
	        messageUpdate:[],   //更新日志
	        pageSize: 6,
	      	activeName: 'first',
	      	dataText:'',
	        currentPage: 1,
	        currentPage2: 1,
	        currentPage3: 1,
	        loading:true,
	      };
	    },
	    created(){
	    	this.getMessage();
	    },
	    computed:{
            alllen(){
            	return this.messageAll.length;
            },
            sestemlen(){
            	return this.messageSestem.length;
            },
            updatelen(){
            	return this.messageUpdate.length;
            }
	    },
	    mounted(){
	    	 setTimeout(()=>{
                this.loading = !this.loading;
	    	 },2000);
	    },	
	    watch:{
	       '$store.state.message.message.all'(){
	       	    let messageData = this.$store.state.message.message.all;
                this.messageAll = messageData;
                messageData.length === 0 ? this.dataText='暂无数据':'';
	        },
	        '$store.state.message.message.sestem'(){
	       	    let messageData = this.$store.state.message.message.sestem;
                this.messageSestem = messageData;
	        },
	        '$store.state.message.message.update'(){
	       	    let messageData = this.$store.state.message.message.update;
                this.messageUpdate = messageData;
	        }
	    },
	    methods: {
	      ...mapActions(['getMessage']),
	      routerPath(path,component){
             this.$router.push(path);
	         bus.$emit("v-component",component);
		  },
	      handleClick(tab, event) {
	        console.log(1, 2);
	      },
	      handleSizeChange(val) {

	        console.log(`每页 ${val} 条`);
	      },
	      handleCurrentChange(val) {
	  
	        console.log(`当前页1: ${val}`);
	      },
	      refresh(){
	      	 this.loading = true;
	      	 this.getMessage();
	      	 setTimeout(()=>{
	      	 	 this.message = this.$store.getters.getDataMessage;
	      	 	 this.loading = false;
                 this.$message({
		           type: 'success',
		           message: '刷新成功!'
	             });
	      	 },2000)
	      }
	    }
	}
</script>

<style scoped lang="less">
	.box-tab{
		margin-top: 10px;
	}
	.box-title{
		display: flex;
		justify-content: flex-start;
		flex-direction: row;
		align-items: center;
		font-size: 14px;
		text-align:left;
		color:#666666;
		line-height:30px;
		span{
			flex:0.03;
			text-align:center;
		}
		span.tt-p{
			flex:0.6;
			padding-left:18px;
			text-align:left;
		}
		span.tt-t{
			flex:0.3;
			text-align:left;
		}
	}
	.box-ul li{
		display: flex;
		justify-content: flex-start;
		flex-direction: row;
		align-items: center;
		font-size: 14px;
		text-align:left;
		line-height:30px;
		span{
			flex:0.03;
			text-align:center;
		}
		span.ul-n{
			color:#666666;
		}
		span.ul-p{
			flex:0.6;
			padding-left:18px;
			text-align:left;
		}
		span.ul-t{
			flex:0.3;
			color:#666666;
			text-align:left;
		}
	}
	.el-pagination{
        text-align:left;
	}
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
</style>


