<template>
	<div id="wchat">
	     <div class="position">
			 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/wchat','vue-wchat')">微信管理</el-breadcrumb-item>
			 </el-breadcrumb>
		     <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="wchat-tab">
		 	  <div class="box-wrap">
		 	  	  <el-button class="p-d" type="primary" @click="dialogVisible = true">添加微信</el-button>
		 	  	  <el-dialog
				  title="添加微信"
				  :visible.sync="dialogVisible"
				  width="30%">
				  <div class="web-input">
				  	  <span class="input-l">微信名称</span>
				  	  <el-input class="input-r" ref="p1" v-model="name" name="name" :disabled="isdisabledFn" placeholder="请输入微信名称"></el-input>
				  </div>
				   <div class="web-input">
				  	  <span class="input-l input-url">&#160;&#160;&#160;微信号</span>
				  	  <el-input class="input-r" ref="p2" v-model="wchat" name="wchat" :disabled="isdisabledFn" placeholder="请输入微信号"></el-input>
				  </div>
				  <div class="web-input">
				  	  <span class="input-l input-describe">选择网站</span>
				  	  <el-select class="input-r input-select" v-model="value" :disabled="isdisabledFn" name="alert" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
				  </div>
				  <span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="wchatConfirm" :disabled="isdisabledFn" :loading="isloading">确 定</el-button>
				    <el-button @click="dialogVisible = false">取 消</el-button>
				  </span>
		     </el-dialog>
		 	  </div>
		 </div>
		 <div class="wchat-del" v-loading="loading">
		 	  <ul>
		 	  	  <li v-for="(item,index) in wchatArr" :key="index">
		 	  	         <el-tooltip class="item" effect="dark" placement="top">
		 	  	              <span slot="content">{{item.url}}</span>
                              <span>{{item.name}}({{item.wchat}})<font @click="wchatDelate(item.uid,item.id)">删除</font></span>
                         </el-tooltip>
		 	  	  </li>
		 	  </ul>
		 </div>
	</div>
</template>


<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'
    import { mapState,mapActions } from 'vuex'

	export default{
		data(){
			return{
				name:'',
				wchat:'',
				value:'',
				loading:true,
				isloading:false,
                dialogVisible:false,
                isdisabledFn:false,
                options: [],
                wchatArr: []
			}
		},
		created(){
			this.getWebsite();
		},
		mounted(){
			setTimeout(()=>{
				//网站添加提示
				let status = this.$store.state.website.website
				if(!status[0]){
                    this.$message({
			           type: 'warning',
			           message: '没有任何网站，请添加网站'
		            });
		            this.loading = false;
                    this.isdisabledFn = true;  
				}else{
					for(let i in status){
						this.wchatManage(status[i]['id'],status[i]['url'],parseInt(i)+1,status.length,1);
					}
				}
			},2000);
		},
		watch:{
			'$store.state.website.website'(){
				let options = [];
				let wchatData = this.$store.state.website.website;
				for(let i in wchatData){
					options.push({
					    value: wchatData[i].id,
					    label: wchatData[i].url
					});
				}
			    this.options = options;
			}
		},
		methods:{
			...mapActions(['getWebsite']),
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
		    wchatManage(ev,url,o,len,stu){
		    	req.post(api.POST_GETBASE,{name:"id",data:ev},1).then(res=>{
		    		 console.log("res",res);
	                 if(o === len){
		                 //最后一组数据  
		          	     if(stu === 2){
                             this.$message({
					           type: 'success',
					           message: '刷新成功!'
					         });
		          	     }else if(stu === 3){
		          	     	 this.$message({
					           type: 'success',
					           message: '删除成功!'
				             });
		          	     }
			          	 this.loading = false;
				      }
				      for(let i in res){
	          	     	this.wchatArr.push({
	          	     		id: res[i]['id'],
	          	     		name: res[i]['name'],
	          	     		wchat: res[i]['wchat'],
	          	     		url: url,
	          	     		uid: ev
	          	     	})
	          	     }  
		    	})
		    },
		    wchatDelate(el,ev){
		    	this.$confirm('确定删除', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
		        }).then(() => {
		           console.log(el,ev);
		           //delete微信
		           req.post(api.POST_WEB3,{
		          	 	uid: el,
		          	 	id: ev
				   },2).then(res=>{
                        if(res.status == 200){
				             this.getWebsite();
				             this.loading = true;
				             this.wchatArr = [];
				             setTimeout(()=>{
                                let webData = this.$store.state.website.website;
                                for(let i in webData){
		                          	this.wchatManage(webData[i]['id'],webData[i]['url'],parseInt(i)+1,webData.length,3);
		                        }
				             },1000);
			          	 }
		           })
		        }).catch(()=>{});
		    },
		    wchatConfirm(){
                if(!this.name){
                	this.$message({
			           type: 'warning',
			           message: '微信名称'
		            });
		            this.$refs.p1.focus();
                }else if(!this.wchat){
                	this.$message({
			           type: 'warning',
			           message: '微信号'
		            });
		            this.$refs.p2.focus();
		            return;
                }else if(!this.value){
                	this.$message({
			           type: 'warning',
			           message: '选择网站'
		            });
                }else{
                	//检索微信号  
                	this.isloading = true;
                	req.post(api.POST_GETBASE,{name:"id",data:this.value},1).then(res=>{
                		for(let i in res){
		          	  	 	 if(res[i].wchat === this.wchat){
                                 this.$message({
						            type: 'warning',
						            message: '微信号重复'
					             }); 
					             this.isloading = false;
					             return;
		          	  	 	 }
		          	  	 }
		          	  	 req.post(api.POST_DATA,{
			          	 	id: this.value,
			          	 	name: this.name,
			          	 	wchat: this.wchat,
			          	 	status: "0"
						 },2).then(res=>{
                             if(res.status == 200){
                                 this.isloading = false;
		          	  	 	 	 this.$message({
						            type: 'success',
						            message: '添加成功!'
					             });
		          	  	 	 }
		          	  	 })
                	})	
                }
		    },
		    refresh(){
	          	this.getWebsite();
	          	setTimeout(()=>{
	          		  let obj = [];
	                  let webData = this.$store.state.website.website;
	                  
	                  console.log("刷新后的数据",webData);
	          	  	  if(webData === 0){
                          this.isdisabledFn = true;
                          this.$message({
					          type: 'warning',
					          message: '没有任何网站，请添加网站'
				          });
	          	  	  }else{	          	  	  	  
	          	  	  	  this.isdisabledFn = false;
	          	  	  	  this.loading = true;
	          	  	  	  this.wchatArr = [];
                          for(let i in webData){
                          	  this.wchatManage(webData[i]['id'],webData[i]['url'],parseInt(i)+1,webData.length,2);
                          }
	          	  	  }
	          	},2000);
		    }
		}
	}
</script>


<style scoped lang="less">
    .wchat-tab{
		margin:15px 0 5px;
		overflow:hidden;
		.p-d{
			float:left ! important;
			margin:10px 0;
		}
	}
    .web-input{
		width:100%;
		padding:0 60px;
		margin-bottom: 15px;
		.input-l,.input-r{
			display:inline-block;
		}
		.input-r{
			width:60%;
			margin-left: 10px;
		}
		.input-select{
			width:40%;
		}
	}
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
	.wchat-del{
		max-width:600px;
		height:450px;
		overflow-x:hidden;
		padding:18px 20px;
		text-align:left;
		ul li{
			width:25%;
			display:inline-block;
			margin-bottom:5px;
			font-size:14px;
			line-height:26px;
			cursor:pointer;
			color:#888888;
		}
		font{
			padding:3px 4px;
			font-size:12px;
			color:#ffffff;
			background-color: #FF5722;
		}
	}
</style>


