<template>
	<div id="phone">
		 <div ref="myCharts" v-show="isShowData" :style="{width: '500px', height: '250px', margin: '0 auto'}"></div>
		 <div :style="{width: '500px', height: '250px', lineHeight: '250px', margin: '0 auto', color: '#666'}" v-show="isShowNan">无数据</div>
	</div>
</template>


<script>
    import { mapState,mapActions } from 'vuex'

	export default{
		data(){
			return{
				isShowNan:true,
				isShowData:false,
                option:{}
			}
		},
		watch:{
			'$store.state.main.main'(){
            	let echartData = this.$store.state.main.main;
            	let engineName = [];  //legend
            	let engineData = [];  //series
                
                if(!echartData[0]){
                     this.isShowNan = true;
            		 this.isShowData = false;
            		 //通过vuex传状态给附近去掉loading
				     this.updateStatusPhone(); 
                }else{
                	for(let i=0; i<echartData.length; i++){
	            		if(engineName.indexOf(echartData[i]['phone']) == -1){
	            			engineName.push(echartData[i]['phone']);
	            		}
	            	}
	            	for(let j=0; j<echartData.length; j++){                                 //搜狗  神马   神马  百度   搜搜   百度  搜狗
	                    if(engineData.length > 0){
	                       let flag = true;
	                       for(let k=0; k<engineData.length; k++){
	                       	   if(engineData[k]['name'] === echartData[j]['phone']){       //搜狗 神马  百度 
	                       	   	   engineData[k]['value']++;
	                       	   	   flag = false;
	                       	   }
	                       }
	                       if(flag){
	                       	   engineData.push({             
		            		   	   value:1,
		            		   	   name:echartData[j]['phone']
	            		       })
	                       }
	                    }else{
	                       engineData.push({              //神马
	            		   	   value:1,
	            		   	   name:echartData[j]['phone']
	            		   })
	                    }                                   //搜狗  神马   神马  百度   搜搜
	            	}
	            	console.log("数据",engineName,engineData);
	            	this.option = {
	            		title: {
					        subtext: '访问设备分布',
					        left: 'center'
					    },
					    tooltip: {
					        trigger: 'item',
					        formatter: '{a} <br/>{b}: {c} ({d}%)',
					        transitionDuration: 0
					    },
					    legend: {
					        orient: 'vertical',
					        left: 10,
					        data: engineName
					    },
					    series: [
					        {
					            name: '访问来源',
					            type: 'pie',
					            radius: ['50%', '70%'],
					            avoidLabelOverlap: false,
					            label: {
					                show: false,
					                position: 'center'
					            },
					            emphasis: {
					                label: {
					                    show: true,
					                    fontSize: '20',
					                    fontWeight: 'normal'
					                }
					            },
					            labelLine: {
					                show: false
					            },
					            data: engineData
					        }
					    ]
	            	}
	            	//初始化echarts实例
					let myCharts = this.$echarts.init(this.$refs.myCharts);
					//绘制图表
					myCharts.setOption(this.option); 
					this.isShowData = true;
					this.isShowNan = false;
				    //通过vuex传状态给附近去掉loading
				    this.updateStatusPhone();
                } 
			}
		},
		methods:{
			...mapActions(['updateStatusPhone','getEngineData']),
            triggerPhone(groups){
            	this.getEngineData(groups);
            }
	    }
	}
</script>

<style scoped lang="less">
	
</style>



