<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import api from '@/config/api.js'
import * as req from '@/public/request.js'

export default {
  name: 'App',
  data(){
     return{
        logoutLimit: 1000 * 60 * 1
     }
  },
  mounted(){
     console.log("环境地址",process.env.API_ROOT);
     this.clearStatus();
  },
  methods:{
     logout(n){
        if(this.$cookies && this.$cookies.get("token") && this.$cookies.get("token") !== ''){
            req.get(api.GET_LOGOUT,{token:this.$cookies.get("token")}).then((res)=>{
                 let code = res;
                 if(code.statusCode === '1'){
                     this.clearCookie();
                     this.$confirm("检测到最近无任何操作，系统自动登出。请重新登录！","提示",{
                        confirmButtonText: '确定',
                        showCancelButton: false,
                        type: 'warning'
                     }).then(()=>{
                         this.$message({
                           type: 'success',
                           message: '退出成功'
                         });
                         this.clearLogout();
                     }).catch(()=>{ 
                         this.clearLogout();
                     })  
                 }else{
                     this.$message({
                        message: "登出失败",
                        type: "error"
                     });
                 }
            }).catch(()=>{});
        }
     },
     clearStatus(){
        let logoutTimer = null;
        let cookie = this.$cookies.get("token");
        let userClearLog = ()=>{
             clearTimeout(logoutTimer);
             logoutTimer = setTimeout(this.logout,this.logoutLimit); 
        }

        if(cookie){
            userClearLog();
        }else{
            this.clearLogout();
        }
 
        document.getElementById("app").addEventListener('keydown', function(e){ userClearLog(); }); //符合五个动作都将重新启动定时器
        document.getElementById("app").addEventListener('mousemove', function(e){ userClearLog(); });
        document.getElementById("app").addEventListener('mousedown', function(e){ userClearLog(); });
        document.getElementById("app").addEventListener('click', function(e){ userClearLog(); });
        document.getElementById("app").addEventListener('scroll', function(e){ userClearLog(); });
     },
     clearCookie(){
        this.$cookies.remove("token");   //清除cookie
     },
     clearLogout(){
        this.$router.push("/login");
        localStorage.removeItem("token"); //删除localStorage记录
        localStorage.removeItem("group");
        localStorage.removeItem("user-infos");
     }
  }
}
</script>



