<template>
	<div id="user">
	    <div class="position">
		 	 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</router-link></el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/user','vue-userinfo')">基本资料</el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		<div class="user-title"><span>设置我的资料</span></div>
		<el-form class="userform" ref="form" :model="form" label-width="80px">
			  <el-form-item label="昵称">
			    <el-input v-model="form.name" name="name"></el-input>
			  </el-form-item>
			  <el-form-item label="用户名">
			   <el-input class="import" v-model="form.username" disabled></el-input>
			  </el-form-item>
              <el-form-item label="性别">
			    <el-radio v-model="form.radio" label="1" name="male">男</el-radio>
                <el-radio v-model="form.radio" label="2" name="male">女</el-radio>
			  </el-form-item>
			  <el-form-item label="手机">
			    <el-input v-model="form.phone" name="phone"></el-input>
			  </el-form-item>
			  <el-form-item label="邮箱">
			    <el-input v-model="form.email" name="email"></el-input>
			  </el-form-item>
			  <el-form-item label="备注">
			    <el-input class="textarea" type="textarea" v-model="form.tips" name="textarea" placeholder="请输入内容"></el-input>
			  </el-form-item>
			  <el-form-item class="primary-btn">
			    <el-button type="primary" @click="onSubmit">确认</el-button>
			    <el-button @click="onReset">清空</el-button>
			  </el-form-item>
         </el-form>
         <div class="user-tips"><span>tips 用户名不可修改，默认为登录用户名</span></div>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'

	export default{
		data(){
			return{
                form: {
		          name: '',
		          username: '',
                  phone: '',
                  email: '',
                  tips: '',
		          radio: '2'
		        },
		        userData:{}
			}
		},
		mounted(){
             if(JSON.parse(localStorage.getItem("user-infos"))){
             	 let obj = JSON.parse(localStorage.getItem("user-infos"));
             	 this.userData = {
             	    name: obj.nickname,
             	    username: obj.username,
             	    phone: obj.phone,
             	    email: obj.email,
             	    tips: obj.textarea,
             	    radio: obj.male
             	 }
             }
		},
		watch:{
            userData(newValue){
               this.form = {
               	  name:newValue.name,
               	  username:newValue.username,
               	  phone:newValue.phone,
               	  email:newValue.email,
               	  tips:newValue.tips,
               	  radio:newValue.radio
               }
            }
		},
		methods:{
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
			onSubmit() {
				this.$confirm('确认修改(上线后对接修改接口)', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          //修改信息提交
			          req.post(api.POST_UPDATE_ADMIN,{
				    		name: this.form.name,
				    		male: this.form.radio,
				    		phone: this.form.phone,
				    		email: this.form.email,
				    		textarea: this.form.tips
				      },2).then(res=>{
				      	  console.log("res",res);
	                      if(res.status == 200){
		          	  	     let obj = JSON.stringify({
	                            "nickname": this.form.name,
	                            "username": this.form.username,
	                            "male": this.form.radio,
	                            "phone": this.form.phone,
	                            "email": this.form.email,
	                            "textarea": this.form.tips
	                          });
	                          localStorage.setItem('user-infos', obj); 
	                          this.$message({
					            type: 'success',
					            message: '修改成功!'
					          });
			          	  }
			         });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消'
		          });          
		        });
		    },
		    onReset(){
                this.form = {
		          name: '',
		          username: '',
                  phone: '',
                  email: '',
                  tips: '',
		          radio: this.form.radio
		        }
		    },
		    refresh(){
		        setTimeout(()=>{
			         let obj = JSON.parse(localStorage.getItem("user-infos"));
	             	 this.userData = {
	             	    name: obj.nickname,
	             	    username: obj.username,
	             	    phone: obj.phone,
	             	    email: obj.email,
	             	    tips: obj.textarea,
	             	    radio: obj.male
	             	 }
                     this.$message({
			           type: 'success',
			           message: '刷新成功!'
		             });
		        },1000);
		    }
		}
	}
</script>


<style scoped lang="less">
	.user-title{
		text-align:left;
		padding-top:20px;
		padding-left:5px;
		span{
			font-size:14px;
		}
	}
	.userform{
		width:260px;
		margin:15px 0 0 15px;
		.textarea{
			width:500px ! important;
		}
		.import{
			&:after{
				display:block;
				content:"*";
				font-size:6px;
				position:absolute;
				left:-65px;
				top:0;
				width:10px;
				color:#f00;
				text-align:center;
			};
		}
	}
    .user-tips{
        position:relative;
    	text-align:left;
    	padding-left:10px;
    	margin-top: 45px;
    	font-size:14px;
    	&:after{
    		display:block;
			content:"*";
			font-size:6px;
			position:absolute;
			left:-1px;
			top:0;
			width:10px;
			color:#f00;
			text-align:center;
    	};
    }
    .position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
</style>


