import * as req from '@/public/request.js'
import api from '@/config/api.js'
import types from '../types.js'

const state = {
      visitor:[],
      options:[]
}

const getters = {
      getVisitor(state){
      	  return state.visitor;
      }
}

const mutations = {
      [types.GET_VISITOR](state,data){
           state.visitor = [];
           state.visitor = data;
      },
      [types.GET_OPTIONS](state,data){
      	   state.options = data;
      	   console.log("接收到的参数options",data);
      }     
}

const actions = {
      getDataVisitor({commit,state},obj){
      	  console.log("传过来的数据",obj);
          req.post(api.POST_VISITOR,{
              datav:obj.data,
              stime:obj.data1,
              etime:obj.data2,
              sel:obj.data3,
              weix:obj.data4.trim(),
              ip:obj.data5.trim()
          },2).then(res=>{
              commit(types.GET_VISITOR,res);
          })
      },
      getDataOptions({commit,state},obj){
          req.post(api.POST_OPTIONS,{name:"datav",data:obj.data},1).then(res=>{
              commit(types.GET_OPTIONS,res);
          })
      }
}

export default{
	state,
	getters,
	mutations,
	actions
}


