<template>
	<div id="project">
		<div class="position">
		 	 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/project','vue-project')">项目管理</el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="project-tab">
		 	 <div class="box-wrap">
		 	    <el-button class="p-d" type="primary" @click="dialogVisible = true">添加项目</el-button>
		        <el-table
				    :data="group.slice((currentPage-1)*pageSize,currentPage*pageSize)"
				    style="width: 100%" v-loading="loading">
				    <template slot="empty">
				            <p>{{dataText}}</p>
				    </template>
				    <el-table-column
				      label="ID"
				      width="40">
				      <template slot-scope="scope">
				        <span style="margin-left: 10px">{{ scope.row.id }}</span>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label="项目名称"
				      width="280">
				      <template slot-scope="scope">
				        <span>{{ scope.row.project }}</span>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label="企业名称"
				      width="280">
				      <template slot-scope="scope">
				        <span>{{ scope.row.company }}</span>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label="状态"
				      width="80">
				      <template slot-scope="scope">
				        <span><font class="g-s">开启中</font></span>
				      </template>
				    </el-table-column>
				    <el-table-column label="操作" width="120">
				      <template slot-scope="scope">
				        <font class="g-d" @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</font>
				      </template>
				    </el-table-column>
				 </el-table>
		         <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="grouplen">
			    </el-pagination>
			 </div>
		 </div>
		 <div class="project-com">
		 	  <el-dialog
				  title="添加项目"
				  :visible.sync="dialogVisible"
				  width="30%">
				  <span class="input-l">项目名称</span>
				  <el-input class="input-r" v-model="projectValue" name="groups" placeholder="请输入内容"></el-input>
				  <span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="projectConfirm" :loading="isloading">确 定</el-button>
				    <el-button @click="dialogVisible = false">取 消</el-button>
				  </span>
		     </el-dialog>
		 </div>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'
    import {mapGetters,mapActions} from 'vuex'
  
	export default{
		data(){
			return{
                group:[],
                pageSize: 6,
                currentPage: 1,
                loading:true,
                isloading:false,
                projectValue: '',
                dataText: '',
                dialogVisible: false
			}
		},
		mounted(){
			let obj = [];
			let groupData = this.$store.state.group.group;
			let company = JSON.parse(localStorage.getItem("user-infos")).nickname;
			setTimeout(()=>{
				for(var i in groupData){
					obj.push({
						"id": groupData[i].id,
						"project": groupData[i].groups,
						"company": company,
						"status": "",
						"operation": ""
					})
				}
				obj.length === 0 ? this.dataText = '暂无数据':'';
				this.group = obj;
				this.loading = !this.loading;
			},2000)
		},
		computed:{
            grouplen(){
            	return this.group.length;
            }
	    },
	    watch:{
          '$store.state.group.group'(){
          	  let obj = [];
          	  let groupData = this.$store.state.group.group;
          	  let company = JSON.parse(localStorage.getItem("user-infos")).nickname;
          	  setTimeout(()=>{
          	  	  this.group = [];  //清空
          	  	  for(var i in groupData){
					obj.push({
						"id": groupData[i].id,
						"project": groupData[i].groups,
						"company": company,
						"status": "",
						"operation": ""
					})
				  }
				  obj.length === 0 ? this.dataText = '暂无数据':'';
				  this.group = obj;
				  this.loading = false;
          	  },2000)
              //console.log("监听到了group更新了",this.$store.state.group.group);
          }
        },
		methods:{
			...mapActions(['getGroup']),
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
			refresh(){ 
	          	  this.loading = true;
	          	  this.getGroup();
	          	  setTimeout(()=>{
	          	  	  let obj = [];
	          	  	  let groupData = this.$store.state.group.group;
	          	  	  let company = JSON.parse(localStorage.getItem("user-infos")).nickname;
	          	  	  for(var i in groupData){
						obj.push({
							"id": groupData[i].id,
							"project": groupData[i].groups,
							"company": company,
							"status": "",
							"operation": ""
						})
					  }
					  obj.length === 0 ? this.dataText = '暂无数据':'';
					  this.group = obj;
					  this.loading = false;
					  this.$message({
			            type: 'success',
			            message: '刷新成功!'
			          });
	          	  },2000)
		    },
		    handleDelete(index, row) {
		    	let groupNow = JSON.parse(localStorage.getItem("group")).data;
		    	if(groupNow === row.id){     //当前组
                    this.$message({
		               type: 'warning',
		               message: '当前组无法删除'
		            });
		            return;
		    	}
		        this.$confirm('确认删除(上线后对接修改接口)', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
		        }).then(() => {
		             //dele用户组
		              req.post(api.POST_GROUP2,{name:"groups",data:row.id},1).then(res=>{
		              	  //当前组下有站点，删除站点
                          for(let i in res.data){
                          	  post(api.POST_GROUP3,{name:"value",data:resp.data[i]['id']},1).then(res=>{
                          	  	  console.log("删除成功!");
                          	  })
			          	  }
			          	  this.getGroup();
		                  this.dialogVisible = false;  
			          	  this.$message({
				            type: 'success',
				            message: '删除成功!'
				          });
		              })
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消'
		          });          
		        });
		    },
		    projectConfirm(){
		    	//添加项目的值， this.projectValue
		    	this.isloading = true;
		    	if(this.projectValue){
                    this.$confirm('确认添加(上线后对接修改接口)', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
			        }).then(() => {
			          //提交前遍历数组是否重复
			          for(let i in this.group){
			          	  if(this.group[i].project === this.projectValue){
			          	  	  this.isloading = false;
			          	  	  this.$message({
					            type: 'warning',
					            message: '已有用户组'
					          });
					          return;
			          	  };
			          }
			          //add用户组
			          req.post(api.POST_GROUP,{name:"groups",data:this.projectValue},1).then(res=>{
                         if(res.status == 200){
			          	  	  this.isloading = false;
					          this.dialogVisible = false;
					          this.getGroup();
	                          this.$message({
					            type: 'success',
					            message: '添加成功!'
					          });
			          	  }
			          })
			        }).catch(() => {
			          this.$message({
			            type: 'info',
			            message: '已取消'
			          });          
			        });
		    	}else{
		    	  this.isloading = false;
		    	  this.$message({
		            type: 'warning',
		            message: '数据无效'
		          });
		    	}
		    },
		    handleSizeChange(val) {
		        console.log(`每页 ${val} 条`);
		    },
		    handleCurrentChange(val) {
		        console.log(`当前页1: ${val}`);
		    },
		    deleteOptions(){
                console.log("delete");
		    }
		}
	}
</script>


<style scoped lang="less">
    #bundle(){
    	.pad{
            color:#fff;
            padding:3px;
            font-size:14px;
    	}              
    }
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
	.project-tab{
		margin:15px 0 5px;
		.p-d{
			float:left ! important;
			margin:10px 0;
		}
		font.g-s{
			background-color:#399cff;
			#bundle.pad();
		}
		font.g-d{
			background-color:#FF5722;
			padding:4px ! important;
			cursor: pointer;
            #bundle.pad();
		}
		.el-icon-delete{
			padding-right:3px;
		}
	}
	.input-r{
		width:30%;
		display:inline-block;
		margin-left: 10px;
	}
</style>


