import * as req from '@/public/request.js'
import api from '@/config/api.js'
import types from '../types.js'


const state = {
     website:[]
}
const getters = {}

const mutations = {
   [types.GET_WEB](state,data){
        state.website = data; 
   }
}
const actions = {
    getWebsite({commit,state}){
       let groups = JSON.parse(localStorage.getItem("group")).data;
       req.get(api.GET_WEBSITE,{groups:groups}).then(res=>{
           commit(types.GET_WEB,res);
       })
    }
}
export default{
	state,
	getters,
	mutations,
	actions
}


