<template>
	<div id="header">
		<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
		  <!--<el-menu-item index="1" class="el-icon-refresh-right b-n"></el-menu-item>-->  
		  <el-menu-item index="2" class="p-r">
		   <div class="el-icon-d-arrow-left" @click="transAside"></div>
	  	   <el-select v-model="value" placeholder="请选择组" @change="selectOptions">
		    <el-option
		      v-for="item in options"
		      :key="item.id"
		      :label="item.groups"
		      :value="item.id">
		    </el-option>
		  </el-select>
		  </el-menu-item>  
		  <el-submenu class="f-r" index="3">
		    <template slot="title">杏璞(管理员)</template>
		    <el-menu-item index="3-1" align="center" @click.native="routerPath('/home/user','vue-userinfo')">基本资料</el-menu-item>
		    <el-menu-item index="3-2" align="center" @click.native="routerPath('/home/modify','vue-modify')">修改密码</el-menu-item>
		    <el-menu-item index="3-3" align="center" @click.native="backOut('/login')">退出</el-menu-item>
		  </el-submenu>
		  <el-menu-item class="f-r" index="4" @click.native="routerPath('/home/message','vue-message')">消息中心</el-menu-item>
		</el-menu>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import { mapState,mapActions } from 'vuex'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'


	export default{
       data() {
	      return {
	        activeIndex: '1',
	        activeIndex2: '1',
	        asideWidth: '',
	        value: '',
	        options: []
	      };
	    },
	    inject:['reload'],
	    mounted(){
	    	if(JSON.parse(localStorage.getItem("group"))){
	    		this.value = JSON.parse(localStorage.getItem("group")).group;
	    	}
	    },
	    watch:{
          '$store.state.group.group'(){
          	  let group;
          	  let obj =  this.$store.state.group.group;
          	  if(JSON.parse(localStorage.getItem("group"))){
                 group = JSON.parse(localStorage.getItem("group")).data;
	    	  }
              this.options = obj;
              for(var i in obj){
              	  if(obj[i]['id'] === group){
              	  	  this.value = obj[i]['groups'];
              	  }
              }
          },
          '$store.state.group.asideWidth'(){
          	   this.asideWidth = this.$store.state.group.asideWidth;
          }
        },
	    methods: {
	      ...mapActions(['getWidth']),
	      handleSelect(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      selectOptions(e){
      	    for(let i in this.options){
      	    	if(this.options[i]['id'] === e){
      	    		localStorage.setItem("group",JSON.stringify({
 					   "data":e,
 					   "group": this.options[i]['groups']	
					}));
					this.$router.push("home?group=" + e);  //重置所有组件
					this.$notify.info({
			          title: '切换中',
			          showClose: false
			        });
					setTimeout(()=>{
                       this.$notify({
				          title: '切换成功',
				          type: 'success',
				          showClose: false
				       }); 
				       this.refresh();
					},2000);
      	    	}
      	    }	//console.log(e);
 		  },
 		  transAside(){
 		  	    this.getWidth();
	      },
 		  routerPath(path,component){
 		  	    this.$router.push(path);
 		  	    bus.$emit("v-component",component);
 		  },
 		  backOut(path){
                req.post(api.POST_LOGINS,{name:"status",data:"0"},1).then(res=>{
                	if(res.status == 200){
                        this.$message({
		                    type: 'success',
		                    message: '退出成功'
		                }); 
		                setTimeout(()=>{
		                	this.$router.push(path);
		                	this.$cookies.remove("token");
		                	localStorage.removeItem("token"); //删除localStorage记录
				            localStorage.removeItem("group");
				            localStorage.removeItem("user-infos");
				            
		                },3000);
                	}
                })
 		  },
 		  refresh(){
                this.reload();
          }
	    }
	}
</script>

<style scoped lang="less">
    .el-select{
        width:150px;
    }
    .b-n{
    	font-size: 18px;
    	border-bottom:none ! important;
    }
    .f-r{
    	float:right;
    }
    .p-r{
    	padding:0 5px;
    }
</style>


