<template>
	<div id="statis">
		<!-- 今日点击复制率 -->
	      <div class="main-box s-c">
	      	 <el-progress type="circle" :percentage="todayCopy" :width="60"></el-progress>
	      	 <el-tooltip class="item" effect="dark" :content="todayTisk" placement="top">
	      	     <span>今日点击复制率</span>
	      	 </el-tooltip>
	      </div>
	      <!-- 今日统计 -->
	 	  <div class="main-box">
	 	  	  <div class="box-compute"><span>今日统计</span><span class="b-c s-t">今日</span></div>
	 	  	  <div class="box-detail">
 	  	  	     <ul>
 	  	  	        <li>
	 	  	  	        <el-tooltip class="item" effect="dark" :content="todayCopyTisk" placement="top-start">
	 	  	  	   	       <span>微信复制</span>
	 	  	  	   	    </el-tooltip>
	 	  	  	   	    <span>{{toCopyer}}<font class="el-icon-wind-power"></font></span>
 	  	  	   	    </li>
 	  	  	   	    <li>
	 	  	  	   	    <el-tooltip class="item" effect="dark" :content="todayVisitTisk" placement="top-start">
	 	  	  	   	       <span>访客</span>
	 	  	  	   	    </el-tooltip>
	 	  	  	   	    <span>{{toVisiter}}<font class="el-icon-wind-power"></font></span>
 	  	  	   	    </li>
 	  	  	     </ul>
	 	  	  </div>
	 	  </div>
          <!-- 昨日统计 -->
          <div class="main-box">
          	  <div class="box-compute"><span>昨日统计</span><span class="b-j s-t">昨天</span></div>
	 	  	  <div class="box-detail">
 	  	  	     <ul>
 	  	  	        <li>
	 	  	  	        <el-tooltip class="item" effect="dark" :content="beforeCopyTisk" placement="top-start">
	 	  	  	   	       <span>微信复制</span>
	 	  	  	   	    </el-tooltip>
	 	  	  	   	    <span>{{beCopyer}}<font class="el-icon-wind-power"></font></span>
 	  	  	   	    </li>
 	  	  	   	    <li>
	 	  	  	   	    <el-tooltip class="item" effect="dark" :content="beforeVisitTisk" placement="top-start">
	 	  	  	   	       <span>访客</span>
	                    </el-tooltip>
	                    <span>{{beVisiter}}<font class="el-icon-wind-power"></font></span>
                    </li>
 	  	  	     </ul>
	 	  	  </div>
          </div>
          <!-- 本月统计 -->
          <div class="main-box">
          	  <div class="box-compute"><span>本月统计</span><span class="b-y s-t">本月</span></div>
	 	  	  <div class="box-detail">
 	  	  	     <ul>
 	  	  	        <li>
	 	  	  	        <el-tooltip class="item" effect="dark" :content="monthCopyTisk" placement="top-start">
	 	  	  	   	       <span>微信复制</span>
	 	  	  	   	    </el-tooltip>
	 	  	  	   	    <span>{{mhCopyer}}<font class="el-icon-wind-power"></font></span>
 	  	  	   	    </li>
 	  	  	   	    <li>
	 	  	  	   	    <el-tooltip class="item" effect="dark" :content="monthVisitTisk" placement="top-start">
	 	  	  	   	       <span>访客</span>
	 	  	  	   	    </el-tooltip>
	 	  	  	   	    <span>{{mhVisiter}}<font class="el-icon-wind-power"></font></span>
 	  	  	   	    </li>
 	  	  	     </ul>
	 	  	  </div>
          </div>
	</div>
</template>


<script>
    import { mapState,mapActions } from 'vuex'

	export default{
		data(){
			return{
               todayCopy:0,
               toCopyer:0,
               toVisiter:0,
               beCopyer:0,
               beVisiter:0,
               mhCopyer:0,
               mhVisiter:0,
               todayTisk:'',
               todayCopyTisk:'',
               todayVisitTisk:'',
               beforeCopyTisk:'',
               beforeVisitTisk:'',
               monthCopyTisk:'',
               monthVisitTisk:''
			}
		},
		watch:{
			'$store.state.main.todayCopy'(){
				 let todayCopy = this.$store.state.main.todayCopy;
				 this.todayCopy = todayCopy[0];
				 this.todayTisk = '今日转化率:'+todayCopy[0].toString()+'%';
				 this.updateStatusHeader();
			},
			'$store.state.main.todayCommit'(){
				 let todayCommit = this.$store.state.main.todayCommit;
				 this.toCopyer = todayCommit[0];
				 this.toVisiter = todayCommit[1];
				 this.todayCopyTisk = '今天复制数量:'+todayCommit[0];
				 this.todayVisitTisk = '今天访客数量:'+todayCommit[1];
			},
			'$store.state.main.beforeCommit'(){
				 let beforeCommit = this.$store.state.main.beforeCommit;
				 this.beCopyer = beforeCommit[0];
				 this.beVisiter = beforeCommit[1];
				 this.beforeCopyTisk = '昨天复制数量:'+beforeCommit[0];
				 this.beforeVisitTisk = '昨天访客数量:'+beforeCommit[1];
			},
			'$store.state.main.monthCommit'(){
				 let monthCommit = this.$store.state.main.monthCommit;
				 this.mhCopyer = monthCommit[0];
				 this.mhVisiter = monthCommit[1];	
				 console.log("本月数据",monthCommit);
				 this.monthCopyTisk = '本月复制数量:'+monthCommit[0];
				 this.monthVisitTisk = '本月访客数量:'+monthCommit[1];			 
			}
		},
		methods:{
			...mapActions(['updateStatusHeader'])
		}
	}
</script>


<style scoped lang="less">
    #bundle(){
    	.li{
            display: flex;
        	justify-content:space-between;
        	flex-direction: row;
        	align-items: center;
    	}            
    }
    .s-c{
    	text-align: center;
    	span{
    		display:block;
    		margin-top: 3px;
    		color:#666666;
    	}
    }
    #statis{
    	width:100%;
		font-size: 14px;
	    padding-top: 10px;
	    #bundle.li();
	    .main-box{
	        flex:1;
	        padding:0 20px;
	        .box-compute{
	        	height:30px;
	        	line-height:30px;
	        	#bundle.li();
	        }
	        .box-detail{
	            padding-top: 6px;
	            ul{
	            	li{
	            		color:#666666;
	            		#bundle.li();
	            	}
	            }
	        }
	        .s-t{
	        	color:#ffffff;
	        	font-size:12px;
	        	line-height: 15px;
	        	padding:2px 5px;
	        	border-radius: 3px;
	        }
	        .b-c{
	        	background-color:#399cff;
	        }
	        .b-j{
	        	background-color:#FF4500;
	        }
	        .b-y{
	        	background-color:#FFB800;
	        }
		}
    }
</style>


