<template>
	<div id="website">
		<div class="position">
		 	 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</router-link></el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/website','vue-website')">网站管理</el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="website-tab">
		 	 <div class="box-wrap">
		 	    <el-button class="p-d" type="primary" @click="dialogVisible = true">添加网站</el-button>
			        <el-table
					    :data="website.slice((currentPage-1)*pageSize,currentPage*pageSize)"
					    style="width: 100%" v-loading="loading">
					    <template slot="empty">
				            <p>{{dataText}}</p>
				        </template>
					    <el-table-column
					      label="ID"
					      width="40">
					      <template slot-scope="scope">
					        <span style="margin-left: 10px">{{ scope.row.id }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="网站名称"
					      width="120">
					      <template slot-scope="scope">
					        <span :title="scope.row.website">{{ scope.row.website }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="网站URL"
					      width="280">
					      <template slot-scope="scope">
					        <span :title="scope.row.url">{{ scope.row.url }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="在线微信"
					      width="260">
					      <template slot-scope="scope">
					        <span :title="scope.row.wchat">{{ scope.row.wchat }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="网站添加代码"
					      width="240">
					      <template slot-scope="scope">
					      <el-popover
						    placement="top-start"
						    width="600"
						    trigger="hover"
						    :content="scope.row.script">
						    <span class="a-s" :title="scope.row.script" slot="reference">{{ scope.row.script }}</span>
						  </el-popover>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="网站描述"
					      width="200">
					      <template slot-scope="scope">
					        <span :title="scope.row.describes">{{ scope.row.describes }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="弹窗样式"
					      width="80">
					      <template slot-scope="scope">
					        <span>{{ scope.row.alert }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column label="操作" width="200">
					      <template slot-scope="scope">
					        <font class="g-t" @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-setting"></i>微信编辑</font>
					        <font class="g-d" @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</font>
					      </template>
					    </el-table-column>
					 </el-table>
				 <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="webLen">
			    </el-pagination>
			    <div class="user-tips"><span>tips 微信管理删除微信后，在线微信号需在微信编辑后同步</span></div>
			    <div class="user-tips m-t"><span>tips 网站URL必须是唯一，当前组不能重复添加</span></div>
			 </div>
		 </div>
		 <!-- 添加站点 -->
		 <div class="website-com">
		 	  <el-dialog
				  title="添加站点"
				  :visible.sync="dialogVisible"
				  width="30%">
				  <div class="web-input">
				  	  <span class="input-l">网站名称</span>
				  	  <el-input class="input-r" ref="p1" v-model="name" name="name" placeholder="请输入站点名称"></el-input>
				  </div>
				   <div class="web-input">
				  	  <span class="input-l input-url">网站URL</span>
				  	  <el-input class="input-r" ref="p2" v-model="url" name="url" placeholder="格式:http://xxx.xxx.xxx/"></el-input>
				  </div>
				   <div class="web-input">
				  	  <span class="input-l input-wchat">在线微信</span>
				  	   <el-input class="input-r" ref="p3" v-model="wchat" name="wchat" placeholder="在线微信默认为空" disabled></el-input>
				  </div>
				   <div class="web-input">
				  	  <span class="input-l input-describe">网站描述</span>
				  	  <el-input class="input-r" ref="p4" v-model="describes" name="describes" placeholder="请输入网站描述"></el-input>
				  </div>
				  <div class="web-input">
				  	  <span class="input-l input-describe">弹窗样式</span>
				  	  <el-select class="input-r input-select" v-model="value" name="alert" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
				  </div>
				  <div class="tips"><span>目前支持两种弹窗样式</span><span class="color-b" @click="open('1')">查看样式一</span><span class="color-b" @click="open('2')">查看样式二</span></div>
				  <span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="websiteConfirm" :loading="isdisabledFn1">确 定</el-button>
				    <el-button @click="dialogVisible = false">取 消</el-button>
				  </span>
		     </el-dialog>
		 </div>
		 <!-- 微信设置 -->
		 <div class="website-edit">
		 	 <el-dialog title="网站微信设置" :visible.sync="dialogFormVisible">
		 	      <div class="no-data" v-show="isShowData"><span>暂无数据</span></div>
		 	      <el-form :model="form">
	                  <el-form-item v-for="(item,index) in formList" :key="item.id">
					     <span>{{item.name}}</span>(<span>{{item.wchat}}</span>)&nbsp;<el-switch v-model="item.status" :active-value="1" :inactive-value="0" @change="chang($event,item.id)"></el-switch>
					  </el-form-item>
				  </el-form>
				  <div slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="openFormSt" :disabled="isdisabledFn" :loading="isloading">确 定</el-button>
				    <el-button @click="dialogFormVisible = false">取 消</el-button>	
				  </div>
		    </el-dialog>
		 </div>
	</div>
</template>


<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'
    import { mapState,mapActions } from 'vuex'
    import imgStyle1 from '@/assets/images/imgtype1.jpg'
    import imgStyle2 from '@/assets/images/imgtype2.jpg'

	export default{
		data(){
			return{
		        name:'',
		        url:'',
                wchat:'',
                describes:'',
                dataText:'',
                uid:'',
                website:[],
				formList:[],
                options: [{
		          value:'0',
		          label:'不使用'
		        },{
		          value:'1',
		          label:'样式一'
		        }, {
		          value:'2',
		          label: '样式二'
		        }],
		        form:{},
		        value:'0',
		        startWcaht:'',
		        isShowData:false,
		        isdisabledFn:false,
		        isdisabledFn1:false,
		        alertStyle1:imgStyle1,
		        alertStyle2:imgStyle2,
		        pageSize: 10,
                currentPage: 1,
                isloading:false,
                loading:true,
                dialogVisible:false,
                dialogFormVisible:false,
                websiteValue:''

			}
		},
		created(){
			this.getWebsite();
		},
		mounted(){
			//解决在没有website的情况下，刷新后先进入微信管理后进入网站管理死加载
			let store = this.$store.state.website.website
			
			this.$nextTick(()=>{
				console.log("store",store);
			})
			if(!store){
				setTimeout(()=>{
                   this.dataText = '暂无数据';
				   this.loading = false;
				},2000);
			}
		},
		computed:{
			webLen(){
				return this.website.length;
			}
		},
		watch:{
            '$store.state.website.website'(){
            	let obj = [];
	          	let webData = this.$store.state.website.website;
	          	setTimeout(()=>{
                    for(var i in webData){
						obj.push({
							"id": webData[i].id,
							"website": webData[i].website,
							"url": webData[i].url,
							"script": webData[i].script,
							"describes": webData[i].describes,
							"wchat":  webData[i].wchat,
							"alert":  webData[i].alert
						})
					}
					obj.length === 0 ? this.dataText = '暂无数据':'';
					this.website = obj;
					this.loading = false; 
	          	 },2000);
            }
		},
		methods:{
			...mapActions(['getWebsite']),
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
		    handleEdit(e,el){
		    	//微信编辑
		    	req.post(api.POST_GETBASE,{name:"id",data:el.id},1).then(res=>{
		    		let obj = [];
                    for(let i in res){
	          	     	obj.push({
	          	     		id: res[i]['id'],
	          	     		name: res[i]['name'],
	          	     		wchat: res[i]['wchat'],
	          	     		status: Number(res[i]['status'])    //数字类型
	          	     	})
	          	     }
	          	     if(obj.length > 0){
		          	  	 this.isdisabledFn = false;
		          	  	 this.isShowData = false;
		          	  }else{
		          	  	 this.isdisabledFn = true;
                         this.isShowData = true;
		          	  }
		          	  this.dialogFormVisible = true;
		          	  this.formList = obj;
			          this.uid = el.id;
		    	})
		    },
		    handleDelete(e,el){
                this.$confirm('确定删除该站点', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
		        }).then(() => {
		           //delete站点
		           req.post(api.POST_WEB2,{name:"id",data:el.id},1).then(res=>{
		           	  if(res.status == 200){
		           	  	 this.getWebsite();
		          	  	 this.$message({
				           type: 'success',
				           message: '删除成功!'
			             });
		          	  }
		           })
		        }).catch(()=>{});
		    },
		    websiteConfirm(){
                if(!this.name){
                	this.$message({
			           type: 'warning',
			           message: '网站名称'
		            });
		            this.$refs.p1.focus();
                }else if(!this.url){
                	this.$message({
			           type: 'warning',
			           message: '网站URL'
		            });
		            this.$refs.p2.focus();
                }else if(this.url.substr(0,7).toLowerCase() != "http://"){
                	this.$message({
			           type: 'warning',
			           message: '网站URL格式错误'
		            });
		            this.$refs.p2.focus();
                }else{
                  this.isdisabledFn1 = true;
                  if(this.website.length > 0){ //有站点
                       for(let i=0; i<this.website.length; i++){
                       	   if(this.website[i]['website'] === this.name || this.website[i]['url'] === this.url){
                       	   	   this.$message({
						            type: 'warning',
						            message: '站点名称或站点url已存在'
						       });
						       this.isdisabledFn1 = false;
                       	   	   return;
                       	   }
                       }
                       //允许提交
                       this.sendform();
                  }else{ //没有站点直接添加站点
                      this.sendform();
                  }
                }
		    },
		    sendform(){
		    	req.post(api.POST_WEB,{
	          	 	name: this.name,
		    		url: this.url,
		    		wchat: this.wchat,
		    		describes: this.describes,
                    alert: this.value,
                    groups: JSON.parse(localStorage.getItem("group")).data          //group组
		    	},2).then(res=>{
                     let that = this;
                     let resData = res[0].id;  //防止上下冲突
                     req.post(api.POST_CREATE,{
			          	   id: resData,
			          	   alert: that.value
					 },2).then(res=>{
                          if(res.status == 200){	
                               req.post(api.POST_ADDSCRIPT,{name:"id",data:resData},1).then(res=>{
                               	    if(res.status == 200){
                                        that.getWebsite();
									    that.dialogVisible = false;
	                                    that.$message({
								            type: 'success',
								            message: '添加成功!'
								        });  
                               	    }
                               })  
                          }
                     })
		    	})
		    },
		    refresh(){
		    	  this.loading = true;
	          	  this.getWebsite();
	          	  setTimeout(()=>{
	          	  	  let obj = [];
	                  let webData = this.$store.state.website.website;
	          	  	  for(var i in webData){
						obj.push({
							"id": webData[i].id,
							"website": webData[i].website,
							"url": webData[i].url,
							"script": webData[i].script,
							"describes": webData[i].describes,
							"wchat":  webData[i].wchat,
							"alert":  webData[i].alert
						})
					  }
					  obj.length === 0 ? this.dataText = '暂无数据':'';
					  this.website = obj;
					  this.loading = false;
					  this.$message({
			            type: 'success',
			            message: '刷新成功!'
			          });
	          	  },2000)
		    },
		    open(e){
		    	if(e === '1'){
                    this.$alert('<span><img src="'+ this.alertStyle1 +'"></span>', '弹窗样式', {
			           dangerouslyUseHTMLString: true
			        }).catch(()=>{});  
		    	}else{
                    this.$alert('<span><img src="'+ this.alertStyle2 +'"></span>', '弹窗样式', {
			           dangerouslyUseHTMLString: true
			        }).catch(()=>{});  
		    	}
		    },
		    openFormSt(){
                let that = this;
                let wchatSt = '';
                let openStart = [];
                that.isloading = true;

                for(let i in that.formList){
                	if(that.formList[i]['status'] === 1){
                        openStart.push(that.formList[i]['wchat']);
             	  	}
             	  	req.post(api.POST_CHANGEDATA,{
		          	   id: that.uid,
		          	   uid: that.formList[i]['id'],
		          	   status: that.formList[i]['status']	
			    	},2).then(res=>{
	                   if(res.status == 200){
	                 		if(i == that.formList.length-1){
	                 			for(let i in openStart){
		                            wchatSt += (openStart[i] +"、");
				    			}
                                req.post(api.POST_CHANGEDATA2,{   //返回当前操作后的网站id，开启的微信号及true
					          	   id: that.uid,
					          	   ustr: wchatSt
							    },2).then(res=>{
                                    if(res.status == 200){
                                        that.isloading = false;
			                            that.dialogFormVisible = false;
			                            that.getWebsite();
			                 			that.$message({
								            type: 'success',
								            message: '设置成功!'
								        });
                                    }
                                })  
	                 		}
	                 	}
	                })
                }             
		    },
		    chang(el,status){
		    	//当前态和id
                console.log(el,status);
		    },
		    handleSizeChange(val) {
		        console.log(`每页 ${val} 条`);
		    },
		    handleCurrentChange(val) {
		        console.log(`当前页1: ${val}`);
		    }
		}
	}
</script>

<style scoped lang="less">
    #bundle(){
    	.pad{
            color:#fff;
            padding:3px;
            font-size:14px;
    	}              
    }
    .position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
   .website-tab{
		margin:15px 0 5px;
		.p-d{
			float:left ! important;
			margin:10px 0;
		}
		font.g-s{
			background-color:#399cff;
			#bundle.pad();
		}
		.g-t{
			background-color:#409EFF;
			padding:6px ! important;
			color:#ffffff;
			 #bundle.pad();
		}
		.g-d{
			background-color:#FF5722;
			padding:6px ! important;
			cursor: pointer;
            #bundle.pad();
		}
		.el-icon-setting,.el-icon-delete{
			padding-right: 3px;
		}
		font{
			display:inline;
			cursor: pointer;
		}
	}
	.web-input{
		width:100%;
		padding:0 60px;
		margin-bottom: 15px;
		.input-l,.input-r{
			display:inline-block;
		}
		.input-r{
			width:60%;
			margin-left: 10px;
		}
		.input-select{
			width:40%;
		}
	}
	.tips{
        padding:0 60px;
        position:relative;
        span:after{
			display: block;
		    content: "*";
		    font-size: 6px;
		    position: absolute;
		    left: 50px;
		    top: 0px;
		    width: 10px;
		    color: #f00;
		    text-align: center;
	    };
	    .color-b{
	    	padding:5px;
	    	margin:0 5px;
	    	display:inline-block;
            background-color: #409EFF;
	    	color:#ffffff;
	    	border-radius:3px;
	    	font-size:12px;
	    	cursor: pointer;
	    }
	} 
	.user-tips{
        position:relative;
    	text-align:left;
    	padding-left:10px;
    	margin-top: 45px;
    	font-size:14px;
    	&:after{
    		display:block;
			content:"*";
			font-size:6px;
			position:absolute;
			left:-1px;
			top:0;
			width:10px;
			color:#f00;
			text-align:center;
    	};
    }
	.form-list{
		width:120px;
		height:40px;
		line-height:40px;
		text-align:center;
		cursor: pointer;
		border-radius: 4px;
		color:#ffffff;
		span.el-icon-check{
			font-size:25px;
		}
	}
	.m-t{
		margin-top: 5px;
	}
	.form-g{
		background-color:#cecccc;
	}
	.form-b{
		background-color:#409EFF;		
	}
	.no-data{
		color:#888888;
		text-align:center;
	}
	/*默认样式修改*/
	.el-dialog__body{
		height:50px ! important;
		overflow-x:auto;
	}
	.el-form-item{
		width:25%;
		display:inline-block;
		margin-bottom: 5px ! important;
	}
</style>


