<template>
	<div id="apidessc">
		 <div class="position">
			  <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/apidessc','vue-apidessc')">api功能说明</el-breadcrumb-item>
			 </el-breadcrumb>
		 </div>
		 <div class="container">
		 	  <h2><span class="l-line"></span>API说明</h2>
		 	  <div class="container-page">
		 	  	   <p>1) 创建项目：可针对竞价、优化、新闻源等不同项目创建不同项目</p>
		 	  	   <p>2) 创建网站：可根据页面创建网站；或可根据账号不同单元创建网站，如此可针对不同维度的数据进行数据分析</p>
		 	  	   <p>3) 替换页面中有微信号的地方为 <font>{{wxhtml}}</font>,并添加引导长按复制文字

（ 注：参数 pos 可从上至下填写不同标识   我们在统计时记录一下这个标识，方便统计访客复制的微信在页面的哪个位置的多    若不填写此参数系统将自动以数字从上至下排列位置）</p>
                   <p>4) 添加代码：添加网站公共JS最后面。（注：代码请添加至网站最底部）代码获取：左侧导航：站点->网站管理</p>
		 	  </div>
		 	  <h2><span class="l-line"></span>功能说明</h2>
		 	  <div class="container-page">
		 	  	   <p>1) 开发中</p>
		 	  </div>
		 </div>
	</div>
</template>

<script>
    import { bus } from '@/public/bus.js'

	export default{
		data(){
			return{}
		},
		computed:{
			wxhtml(){
				return '<span name="wx_th" flag="wx" class="wxbtn" >微信号</span>';
			}
		},
		methods:{
		    routerPath(path,component){
	             this.$router.push(path);
		         bus.$emit("v-component",component);
		    }
		}
	}
</script>

<style scoped lang="less">
    @size:14px;
    @height:40px;
	.container{
		width:100%;	
		position:relative;
		padding:15px 0;
		h2{
			height: @height;
			line-height:@height;
			font-size:@size;
			text-align:left;
			font-weight: normal;
			padding-left: 15px;
		}
		.l-line{
			display: block;
			position:relative;
		    &:after{
			   display:block;
			   position:absolute;
			   left:-10px;
			   top:0;
               content:"";
               width:3px;
               height:@height;
               background-color: #399cff;
			}
		}
		.container-page{
			padding:0 15px;
			text-align:left;
			font-size:@size;
			p{
				line-height:30px;
			}
			font{
				color:#399cff;
			}
		}
	}

</style>


