<template>
	<div id="modify">
		<div class="position">
		 	 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/modify','vue-modify')">修改密码</el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		<div class="user-title"><span>修改登陆密码</span></div>
		<el-form class="userform" ref="form" :model="form" label-width="80px">
			  <el-form-item label="当前密码">
			    <el-input ref="p1" v-model="form.passowrd" type="password"></el-input>
			  </el-form-item>
			  <el-form-item label="新密码">
			   <el-input ref="p2" class="import" v-model="form.newpassword" name="pwd" type="password"></el-input>
			  </el-form-item>
              <el-form-item label="确认密码">
			   <el-input ref="p3" class="import" v-model="form.confirmnewpassword" type="password"></el-input>
			  </el-form-item>
			  <el-form-item class="primary-btn">
			    <el-button type="primary" @click="onSubmit" :loading="isloading">确认</el-button>
			  </el-form-item>
         </el-form>
         <div class="user-tips"><span>tips 新密码必须6到16个字符</span></div>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import * as req from '@/public/request.js'

	export default{
		data(){
			return{
               form: {
		          passowrd: '',
		          newpassword: '',
                  confirmnewpassword: ''
		       },
		       username:'',
		       isloading: false
			}
		},
		mounted(){
            if(JSON.parse(localStorage.getItem('user'))){
                 this.username = JSON.parse(localStorage.getItem('user')).username;
            }
		},
		methods:{
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
			onSubmit() {
                if(!this.form.passowrd){
                	this.$message({
                        type: 'warning',
                        message: '必填项不能为空'
                    });
                    this.$refs.p1.focus();
                }else if(this.form.newpassword.length > 16 || this.form.newpassword.length < 6){
                	this.$message({
                        type: 'warning',
                        message: '必须6到16个字符'
                    });
                    this.$refs.p2.focus();
                }else if(this.form.newpassword !== this.form.confirmnewpassword){
                	this.$message({
                        type: 'warning',
                        message: '两次密码输入不一致'
                    });
                    this.$refs.p3.focus();
                }else{
                	this.isloading = true;
                    req.post(api.POST_UPDATE_PASSWORD,{pwd:this.form.passowrd,newpwd:this.form.newpassword},2).then(res=>{
                    	  console.log("修改密码状态",res);
                    	  if(res.status == 200){
                    	  	  this.isloading = false;
                              this.$message({
					            type: 'success',
					            message: '修改成功!'
					          });
					          this.form = {
					             passowrd: '',
					             newpassword: '',
			                     confirmnewpassword: ''
						      }
                    	  }else if(res.status == 400){
                    	  	  this.isloading = false;
                              this.$message({
		                         type: 'warning',
		                         message: '当前密码错误'
			                  });
			                  this.$refs.p1.focus(); 
                    	  }
			        });
                } 
		    },
			refresh(){
			     setTimeout(()=>{
			      	this.form = {
			          passowrd: '',
			          newpassword: '',
	                  confirmnewpassword: ''
			        }
			        this.$message({
			          type: 'success',
			          message: '刷新成功!'
		            });
		         },1000);		      	  
		    }
		}
	}
</script>

<style scoped lang="less">
    .user-title{
		text-align:left;
		padding-top:20px;
		padding-left:5px;
		span{
			font-size:14px;
		}
	}
	.userform{
		width:260px;
		margin:15px 0 0 15px;
	}
	.user-tips{
        position:relative;
    	text-align:left;
    	padding-left:10px;
    	margin-top: 45px;
    	font-size:14px;
    	&:after{
    		display:block;
			content:"*";
			font-size:6px;
			position:absolute;
			left:-1px;
			top:0;
			width:10px;
			color:#f00;
			text-align:center;
    	};
    }
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
</style>


