<template>
	<div id="manage">
	    <el-row>
	        <el-col :span="5"><div class="manage-title">分组选择&nbsp;</div></el-col>
	    	<el-col :span="12">
	    		<el-select v-model="value" placeholder="请选择一个分组" @change="selectOptions">
				    <el-option-group
				      v-for="group in options"
				      :key="group.label"
				      :label="group.label">
				      <el-option
				        v-for="item in group.options"
				        :key="item.id"
				        :label="item.groups"
				        :value="item.id">
				      </el-option>
				    </el-option-group>
				 </el-select>
	    	</el-col>
            <el-col :span="7"><el-button type="primary" @click="enterOptoions">选择进入</el-button></el-col> 
	    </el-row>
	</div>
</template>

<script>
    import '@/style/theme.css'
    import {mapGetters,mapActions} from 'vuex'

	export default{
		data(){
			 return {
		        options: [], 
		        value: '',
		        group: [],
		        groupData:[],
		        selectValue: null
		      }
 		},
 		created(){
 			 this.getGroup();
 			 //this.$store.dispatch("getGroup");   //登陆成功发送组请求
 		},
 		watch:{
 			'$store.state.group.group'(){
 				let obj = this.$store.state.group.group;
 		    	let nickname = JSON.parse(localStorage.getItem("user-infos")).nickname;

 		    	this.group = obj;
                this.options = [{
		          label: nickname,
		          options: obj
                }]
 			}
 		},
 		methods:{
 			...mapActions(['getGroup']),
 			selectOptions(e){
 				this.selectValue = e;
 			},
 			enterOptoions(){
 				if(!this.selectValue){
                    this.$message({
			          message: '参数错误，未选择分组',
			          type: 'warning'
			        });
 				}else{
 					for(let i in this.group){
 						if(this.selectValue	=== this.group[i]['id']){
 							let obj = JSON.stringify({
		 						"data":this.selectValue,
		 						"group": this.group[i]['groups']	
		 					})
		 					localStorage.setItem("group",obj);
		 					this.$router.push("home?group="+this.selectValue);
		 					console.log("enter--yes");
 						}
 					}
 				}
 			}
 		}
	}
</script>

<style scoped lang="less">
	#manage{
		width:350px;
		margin:180px auto 0;
		color:#555555;
		.manage-title{
			height:40px;
			line-height: 40px;
		}
	}
	.el-button--primary{
	   margin-left: 10px ! important;
	}
	.el-button{
	   padding:12px 10px ! important;
	}
</style>


