<template>
	<div id="home">
         <div class="main-scroll">
              <el-backtop :visibility-height="100" :bottom="60"></el-backtop> <!-- scrolltop-->
         </div>
	       <div class="aside-container" :style="{width:getAsideWidth+'px'}">
     	       	<vue-aside></vue-aside>
     	   </div>
         <div class="main-container" :style="{paddingLeft:getAsideWidth+'px'}">
             <el-container>
                 <el-header>
                    <vue-header></vue-header>
                 </el-header>
                 <el-main>
                     <keep-alive v-if="isRouterAlive">
                          <component :is="flag"></component>
                     </keep-alive>
                 </el-main>
             </el-container>
         </div>
	</div>
</template>

<script>
    import Aside from './aside.vue'
    import Header from './header.vue'
    import Main from './main.vue'
    import Apidessc from './apidessc.vue'
    import Message from './message.vue'
    import Userinfo from './userinfo.vue'
    import Modify from './modify.vue'
    import Project from './project.vue'
    import Website from './website.vue'
    import Wchat from './wchat.vue'
    import Visitor from './visitor.vue'

    import { bus } from '@/public/bus.js'
    import { mapState,mapGetters,mapActions } from 'vuex'

	  export default{
       provide(){
           return{
               reload:this.reload
           }
       },
       data(){
       	   return{
               group: '',
               messageStatus: '',
               flag: 'vue-main',
               fullscreenLoading: false,
               isRouterAlive: true
       	   }
       },
       created(){
       	  if(JSON.parse(localStorage.getItem("token")) && JSON.parse(localStorage.getItem("token")).value && JSON.parse(localStorage.getItem("group"))){
               let groups = JSON.parse(localStorage.getItem("group")).data;
               this.getGroup();
       	  }else{
       	  	   this.$router.push("login");
       	  }	
       },
       mounted(){ 
       	     //分组
       	     this.group = this.$route.query;
       	     //获取$emit发送数据接收
       	     bus.$on("v-component",(n)=>{
       	     	  this.flag = n;
                console.log("this",n)
       	     })
       },
       computed:{
          ...mapGetters(['getAsideWidth'])
       },
       components:{
       	  "vue-aside":Aside,
       	  "vue-header":Header,
       	  "vue-main":Main,
       	  "vue-apidessc":Apidessc,
       	  "vue-message":Message,
          "vue-userinfo":Userinfo,
          "vue-modify":Modify,
          "vue-project":Project,
          "vue-website":Website,
          "vue-wchat":Wchat,
          "vue-visitor":Visitor
       },
       methods:{
          ...mapActions(['getGroup']),
          reload:function(){
               this.isRouterAlive = false;
               this.$nextTick(()=>{
                   this.isRouterAlive = true;
               })
          }
       }
	}
</script>

<style scoped lang="less">
     @height:auto;
     #home{
     	height:@height;
     }
     .el-header{
     	padding:0;
     }
     .aside-container{
     	width:220px;
      position: fixed;
      top:0;
	  	left:0;
	  	overflow-x: hidden;
	  	height:100%;
	  	z-index:1001;
      transition:width .2s linear;
      -webkit-transition:width .2s linear;
	  	background-color: rgb(84, 92, 100);
     }
     .main-container{
       	padding-left: 220px;
        transition:padding .2s linear;
        -webkit-transition:padding .2s linear;
       	.el-header, .el-footer {
  		    background-color: #B3C0D1;
  		    color: #333;
  		    text-align: center;
  		    line-height: 60px;
  		 }
  		 .el-aside {
  		    background-color: #D3DCE6;
  		    color: #333;
  		    height:@height;
  		    text-align: center;
  		    line-height: 200px;
  		    transition: all .3s;
  	      -webkit-transition: all .3s;
  		 }
  		 .el-main {
  		    background-color: none;
  		    color: #333;
  		    text-align: center;
  		 }
     }
     .el-backtop{
       background-color:#c5c5c5;
     }
</style>


