<template>
	<div id="enline">
		<div ref="myCharts" :style="{width: '800px', height: '350px', margin: '0 auto'}"></div>
	</div>
</template>

<script>
    import { mapState,mapActions } from 'vuex'

	export default{
		data(){
			return{
                option:{}
			}
		},
		watch:{
           '$store.state.main.week'(){
           	   let week = this.$store.state.main.week;
           	   let date = week.date;
           	   let a=[],a1=[],b=[],b1=[],c=[],c1=[],d=[],d1=[],e=[],e1=[],f=[],f1=[],g=[],g1=[];
               
               if(week.status !== '0' && week.wchat.length > 0){ 
                   for(let i in week){
                   	   if(i === 'status' || i === 'wchat' || i === 'date'){ continue;}
                   	   if(week[i].length > 0){ //判断当前week[i]的长度是否为0
                   	   	  for(let n=0; n<week[i].length; n++){
                   	   	  	 for(let j=0; j<week.wchat.length; j++){                 	   	  	 	 
                   	   	  	 	 if(week[i][n]['wchat'] === week.wchat[j]['wchat']){
                   	   	  	 	 	 switch(i){
                   	   	  	 	 	 	case 'a': a.push(1); break;
                   	   	  	 	 	 	case 'a1': a1.push(1); break;
                   	   	  	 	 	 	case 'b': b.push(1); break;
                   	   	  	 	 	 	case 'b1': b1.push(1); break;
                   	   	  	 	 	 	case 'c': c.push(1); break;
                   	   	  	 	 	 	case 'c1': c1.push(1); break;
                   	   	  	 	 	 	case 'd': d.push(1); break;
                   	   	  	 	 	 	case 'd1': d1.push(1); break;
                   	   	  	 	 	 	case 'e': e.push(1); break;
                   	   	  	 	 	 	case 'e1': e1.push(1); break;
                   	   	  	 	 	 	case 'f': f.push(1);  break;
                   	   	  	 	 	 	case 'f1': f1.push(1); break;
                   	   	  	 	 	 	case 'g': g.push(1); break;
                   	   	  	 	 	 	case 'g1': g1.push(1); break;
                   	   	  	 	 	 }
                   	   	  	 	 }
                   	   	  	 }
                   	   	  }
                   	   }else{
                   	   	   continue;
                   	   }
                   }
                   week = {a,a1,b,b1,c,c1,d,d1,e,e1,f,f1,g,g1}
	           	   console.log("week",a,a1,b,b1,c,c1,d,d1,e,e1,f,f1,g,g1);
               }
               

               this.option = {
                    tooltip: {
				        trigger: 'axis'
				    },
				    legend: {
				        data: ['复制', '访客']
				    },
				    grid: {
				        left: '3%',
				        right: '4%',
				        bottom: '3%',
				        containLabel: true
				    },
				    toolbox: {
				        feature: {
				            saveAsImage: {}
				        }
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: date
				    },
				    yAxis: {
				        type: 'value'
				    },
				    series: [
				        {
				            name: '复制',
				            type: 'line',
				            //stack: '总量',
				            data: [week.g.length, week.f.length, week.e.length, week.d.length, week.c.length, week.b.length, week.a.length]
				        },
				        {
				            name: '访客',
				            type: 'line',
				            //stack: '总量',
				            data: [week.g1.length, week.f1.length, week.e1.length, week.d1.length, week.c1.length, week.b1.length, week.a1.length]
				        }
				    ]
               }
			    //初始化echarts实例
				let myCharts = this.$echarts.init(this.$refs.myCharts);
				//绘制图表
				myCharts.setOption(this.option);
                //通过vuex传状态给附近去掉loading
				this.updateStatusWeek();  
           	    console.log("周数据",this.$store.state.main.week);
           }
		},
		methods:{
			...mapActions(['updateStatusWeek'])
		}
	}
</script>

<style scoped lang="less">
	#enline{
		padding-top:36px;
	}
</style>


