<template>
	<div id="dislist">
		<div class="dist"><span>地区</span><span>人数</span></div>
		<div class="dist" v-for="(item,index) in city" :key="index"><span>{{item.cityName}}</span><span>{{item.num}}</span></div>
	</div>
</template>

<script>
	export default{
		data(){
		    return{
                city:[     //地域
                   {
                   	  cityName: "西藏",
                   	  num: "0"
                   },
                   {
                   	  cityName: "青海",
                   	  num: "0" 
                   },
                   {
                      cityName: "宁夏",
                   	  num: "0"
                   },
                   {
                   	  cityName: "海南",
                   	  num: "0"
                   },
                   {
                   	  cityName: "甘肃",
                   	  num: "0" 
                   },
                   {
                      cityName: "贵州",
                   	  num: "0"
                   },
                   {
                   	  cityName: "新疆",
                   	  num: "0"
                   },
                   {
                   	  cityName: "云南",
                   	  num: "0" 
                   },
                   {
                      cityName: "重庆",
                   	  num: "0"
                   },
                   {
                   	  cityName: "吉林",
                   	  num: "0"
                   },
                   {
                   	  cityName: "山西",
                   	  num: "0" 
                   },
                   {
                      cityName: "天津",
                   	  num: "0"
                   },
                   {
                   	  cityName: "江西",
                   	  num: "0"
                   },
                   {
                   	  cityName: "广西",
                   	  num: "0" 
                   },
                   {
                      cityName: "陕西",
                   	  num: "0"
                   },
                   {
                      cityName: "黑龙江",
                   	  num: "0"
                   },
                   {
                   	  cityName: "内蒙古",
                   	  num: "0"
                   },
                   {
                   	  cityName: "安徽",
                   	  num: "0" 
                   },
                   {
                      cityName: "北京",
                   	  num: "0"
                   },
                   {
                   	  cityName: "福建",
                   	  num: "0"
                   },
                   {
                   	  cityName: "上海",
                   	  num: "0" 
                   },
                   {
                      cityName: "湖北",
                   	  num: "0"
                   },
                   {
                      cityName: "湖南",
                   	  num: "0"
                   },
                   {
                   	  cityName: "四川",
                   	  num: "0"
                   },
                   {
                   	  cityName: "辽宁",
                   	  num: "0" 
                   },
                   {
                      cityName: "河北",
                   	  num: "0"
                   },
                   {
                   	  cityName: "河南",
                   	  num: "0"
                   },
                   {
                   	  cityName: "浙江",
                   	  num: "0" 
                   },
                   {
                      cityName: "山东",
                   	  num: "0"
                   },
                   {
                      cityName: "江苏",
                   	  num: "0"
                   }
                ]
            }
		},
		watch:{
			'$store.state.main.main'(){
				let cityData = this.$store.state.main.main;
				try{
					for(let i=0; i<this.city.length; i++){   //地域
						for(let j=0; j<cityData.length; j++){
							if(cityData[j]['city'].indexOf(this.city[i]['cityName']) != -1){
	                            this.city[i]['num'] = parseInt(this.city[i]['num']) + 1;
							}
						}
					}
				}catch(e){

				}
			}
		}
	}
</script>

<style scoped lang="less">
   #bundle(){
    	.row{
            display: flex;
        	justify-content:space-between;
        	flex-direction: row;
        	align-items: center;
    	}
    	.column{
    		display: flex;
    		justify-content:start;
    		flex-direction:column;
    		align-items:center;
    	}              
    }
	#dislist{
        width:100%;
        height:500px;
        overflow-y:auto;
        #bundle.column();
        .dist{
        	width:100%;
        	line-height:32px;
        	#bundle.row();
        	span{
        		flex:1;
        		padding-left:10px;
        		text-align:left;
        		color:#666;
        		border:1px solid #e6e6e6;
        		border-bottom: 0;
        		border-right: 0;
        	}
        	span:last-child{
        		border-right:1px solid #e6e6e6;
        	}
        }
        .dist:last-child{
            border-bottom:1px solid #e6e6e6;
        }
	}
</style>


