import * as req from '@/public/request.js'
import api from '@/config/api.js'
import types from '../types.js'

const state = {
	  statusEngine:true,
      statusPhone:true,
      statusHeader:true,
      statusWeek:true,
      todayCopy:[],
      todayCommit:[],
      beforeCommit:[],
      monthCommit:[],
      main:[],
      week:[]
}

const getters = {

}

const mutations = {
	 [types.GET_HEADER](state,obj){
        if(!state.statusHeader){    //loading状态
	         state.statusHeader = true;
		}
		console.log("获取到的obj",obj);
		//无今天复制
	 	if(!obj.copyToday.length){  
            state.todayCopy = [0];
	 	}else{ //有今日复制
            state.todayCopy = [parseInt((obj.copyToday.length/obj.visitToday.length*100).toFixed(0),10)]; 
	 	}

        if(!obj.copyToday.length){
            if(!obj.visitToday.length){
                state.todayCommit = [0,0];
            }else{
                state.todayCommit = [0,obj.visitToday.length];
            }
        }else{
        	state.todayCommit = [obj.copyToday.length,obj.visitToday.length] //今日统计
        }
        //无昨天复制;
        if(!obj.copyBeforeToday.length){
        	if(!obj.visitBeforeToday.length){
                state.beforeCommit = [0,0];
        	}else{
        		state.beforeCommit = [0,obj.visitBeforeToday.length];
        	}
        }else{
        	state.beforeCommit = [obj.copyBeforeToday.length,obj.visitBeforeToday.length] //昨日统计
        }
        //无本月数据
        if(!obj.copyMonth.length){
        	if(!obj.visitMonth.length){
               state.monthCommit = [0,0];
        	}else{
        	   state.monthCommit = [0,obj.visitMonth.length];
        	}
        }else{
        	state.monthCommit = [obj.copyMonth.length,obj.visitMonth.length] //本月统计
        }
	 },
     [types.GET_ECHART](state,obj){
     	  if(!state.statusEngine && !state.statusPhone){   //loading状态
              state.statusEngine = true;
              state.statusPhone = true;
	      }
     	  if(!obj){   //无复制数据
              state.main = [0];
     	  }else{ //有复制数据
	     	  state.main = obj;  //访客来源及访客设备echarts
     	  }
     },
     [types.GET_WEEK](state,obj){
     	  if(!state.statusWeek){
     	  	  state.statusWeek = true;
     	  }
     	  state.week = obj;
     },
     [types.UPDATE_STATUS](state){
     	  state.statusEngine = !state.statusEngine;    //loading状态   
     },
     [types.UPDATE_STATUS1](state){
          state.statusPhone = !state.statusPhone;      //loading状态
     },
     [types.UPDATE_STATUS2](state){
          state.statusHeader = !state.statusHeader;      //loading状态
     },
     [types.UPDATE_STATUS3](state){
          state.statusWeek = !state.statusWeek;      //loading状态
     }
} 

const actions = {
	getHeaderData({commit,state},obj){
		req.post(api.POST_HEADER,{name:"data",data:obj},1).then(res=>{
		   console.log("promise",res);
           commit(types.GET_HEADER,res);
		})
	},
    getEngineData({commit,state},obj){
    	req.post(api.POST_ECHARTS,{name:"data",data:obj},1).then(res=>{
    		console.log("promise1",res);
    		commit(types.GET_ECHART,res);
    	})
    },
    getWeekData({commit,state},obj){
    	req.post(api.POST_WEEK,{data:obj.data,select:obj.select,status:obj.status},2).then(res=>{
    		console.log("promise2",res);
            commit(types.GET_WEEK,res);
    	})    
    }, 
    updateStatusEngine({commit}){
         commit(types.UPDATE_STATUS);
    },
    updateStatusPhone({commit}){
         commit(types.UPDATE_STATUS1);
    },
    updateStatusHeader({commit}){
    	 commit(types.UPDATE_STATUS2);
    },
    updateStatusWeek({commit}){
         commit(types.UPDATE_STATUS3);
    }
}

export default{
	state,
	getters,
	mutations,
	actions
}


