<template>
	<div id="visitor">
		<div class="position">
			 <el-breadcrumb separator="/">
				  <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">首页</el-breadcrumb-item>
				  <el-breadcrumb-item @click.native="routerPath('/home/visitor','vue-visitor')">实时数据访客</el-breadcrumb-item>
			 </el-breadcrumb>
		     <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="visitor-tab">
		      <div class="box-wrap m-a">
		            <div class="el-tips m-l"><span>日期</span>&nbsp;&nbsp;</div>
		            <div class="el-date m-l">
		            	<el-date-picker
					      v-model="value1"
					      type="daterange"
					      range-separator="至"
					      start-placeholder="开始日期"
					      end-placeholder="结束日期"
					      value-format="yyyyMMdd"
					      @change="dateChange">
				        </el-date-picker>
		            </div>
		            <div class="el-tips m-l"><span>访问网站</span>&nbsp;&nbsp;</div>
				    <div class="el-option m-l">
				    	<el-select v-model="value" placeholder="选择统计网站" @change="selectOptions">
					      <el-option
					      width="80"
					      v-for="item in options"
					      :key="item.id"
					      :label="item.website"
					      :value="item.id">
					      </el-option>
					    </el-select>
				    </div>
				    <div class="el-tips m-l"><span>微信号</span>&nbsp;&nbsp;</div>
				    <div class="el-weixin m-l"><span><el-input class="" v-model="weixin" placeholder="请输入微信号"></el-input></span></div>
				    <div class="el-tips m-l"><span>访问ip</span>&nbsp;&nbsp;</div>
				    <div class="el-ip m-l"><span><el-input class="" v-model="ip" placeholder="请输入访问ip"></el-input></span></div>
				    <div class="el-search m-l"><el-button type="primary" icon="el-icon-search" size="medium" @click="sendSearchVistor" :disabled="isdisabledFn">搜索</el-button></div>
				    <!--<div class="el-down-xls m-l"><el-button type="primary" icon="el-icon-folder-opened" size="medium" @click="sendToExcel">导出到Excel</el-button></div>
				    <div class="el-down-csv m-l"><el-button type="primary" icon="el-icon-folder-opened" size="medium" @click="sendToCsv">导出到Csv</el-button></div>-->
				    <div class="el-download m-l">
			    	  <el-popover
					    placement="bottom"
					    width="100"
					    trigger="manual"
					    v-model="visible">
				        <span class="popover-s" @click="sendToExcel">导出到Excel文件</span>
				        <span class="popover-s" @click="sendToCsv">导出到Csv文件</span>	
					    <el-button slot="reference" icon="el-icon-folder-opened" @click="visible = !visible"></el-button>
					  </el-popover>
				    </div>
		      </div>
		 	  <div class="box-wrap">
		 	  	   <el-table
					    :data="dataVisitor.slice((currentPage-1)*pageSize,currentPage*pageSize)"
					    style="width: 100%" v-loading="loading">
					    <template slot="empty">
				            <p>{{dataText}}</p>
				        </template>
					    <el-table-column
					      label="ID"
					      width="80">
					      <template slot-scope="scope">
					        <span style="margin-left: 10px">{{ scope.row.id }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="复制时间"
					      width="180">
					      <template slot-scope="scope">
					        <span>{{ scope.row.time }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="复制微信"
					      width="120">
					      <template slot-scope="scope">
					        <span>{{ scope.row.wchat }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="搜索关键词"
					      width="120">
					      <template slot-scope="scope">
					        <span :title="scope.row.keyword">{{ scope.row.keyword }}</span>
					      </template>
					    </el-table-column>
	                     <el-table-column
					      label="搜索引擎"
					      width="80">
					      <template slot-scope="scope">
					        <span>{{ scope.row.engine }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="ip"
					      width="150">
					      <template slot-scope="scope">
					        <span>{{ scope.row.ip }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="省市区"
					      width="180">
					      <template slot-scope="scope">
					        <span :title="scope.row.city">{{ scope.row.city }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="访问网站"
					      width="120">
					      <template slot-scope="scope">
					        <span>{{ scope.row.web }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="访问域名"
					      width="200">
					      <template slot-scope="scope">
					        <span :title="scope.row.url">{{ scope.row.url }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="访问url"
					      width="280">
					      <template slot-scope="scope">
					        <span :title="scope.row.urls">{{ scope.row.urls }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="手机型号"
					      width="120">
					      <template slot-scope="scope">
					        <span>{{ scope.row.phone }}</span>
					      </template>
					    </el-table-column>
					    <el-table-column
					      label="复制方式"
					      width="120">
					      <template slot-scope="scope">
					        <span>{{ scope.row.copy }}</span>
					      </template>
					    </el-table-column>
					 </el-table>
					  <el-pagination
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page.sync="currentPage"
				      :page-size="pageSize"
				      layout="total, prev, pager, next"
				      :total="visitorLen">
				    </el-pagination>
		 	  </div>
		 </div>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import { bus } from '@/public/bus.js'
    import { mapState } from 'vuex'

	export default{
		data(){
			return{
			   ip:'',
			   weixin:'',
			   value:'',
			   value1:'',
			   selected:'',
			   startTime:'',
			   endTime:'',
			   currentPage:1,
			   pageSize:20,
			   dataText:'',
			   loading:true,
			   visible: false,
			   isdisabledFn:true,
			   options: [],
               dataVisitor:[]
			}
		},
		created(){
			let group = JSON.parse(localStorage.getItem("group")).data;
            this.storeDispath(group,this.startTime,this.endTime,this.selected,this.weixin,this.ip); //表数据
            this.storeDispath2(group); //统计网站
		},
		computed:{
            visitorLen(){
            	return this.dataVisitor.length;
            }
		},
		watch:{
           '$store.state.visitor.visitor'(){
           	   let visitorData = this.$store.state.visitor.visitor;  
           	   let group = JSON.parse(localStorage.getItem("group")).data;  
               
               if(visitorData !== 0){
                   setTimeout(()=>{
                   	   this.dataVisitor = [];
	           	   	   this.dataVisitor = visitorData;
                   	   this.loading = false;
                   	   this.isdisabledFn = false;
		           },2000);
               }else{
               	   this.setTimeVisitor();
               }       	  
           },
           '$store.state.visitor.options'(){
               let optionsData = this.$store.state.visitor.options;
               this.options = optionsData;
           }
		},
		methods:{
			routerPath(path,component){
                this.$router.push(path);
	      	    bus.$emit("v-component",component);
		    },
		    refresh(){
		    	let group = JSON.parse(localStorage.getItem("group")).data;
		    	this.loading = true;
	            setTimeout(()=>{
	    	        this.value = '';
			    	this.value1 = '';
			    	this.startTime = '';
			    	this.endTime = '';
			    	this.weixin = '';
			    	this.selected = '';
			    	this.ip = '';
                    this.storeDispath(group,this.startTime,this.endTime,this.selected,this.weixin,this.ip); //表数据 
                    this.setTimeVisitor();
	            },2000);	    	
		    },
		    dateChange(val){
		    	if(val !== null){
		    		this.startTime = val[0].slice(0,4)+"-"+val[0].slice(4,6)+"-"+val[0].slice(6,8);    //日期过滤
		    		this.startTime += ' 00:00:00'; //添加时分秒
                    this.endTime = val[1].slice(0,4)+"-"+val[1].slice(4,6)+"-"+val[1].slice(6,8);
                    this.endTime += ' 23:59:59'; //添加时分秒
		    	}
		    },
		    sendSearchVistor(){
		    	let group = JSON.parse(localStorage.getItem("group")).data;
		    	this.loading = true;
                this.storeDispath(group,this.startTime,this.endTime,this.selected,this.weixin,this.ip); //表数据 
                this.setTimeVisitor();
                //console.log(this.startTime,this.endTime,this.selected,this.weixin,this.ip);
		    },
		    selectOptions(e){
		    	this.options.forEach((value,index)=>{
		    		if(value['id'] == e){
		    		  	this.selected = value['website'];
		    		    return;
		    		}
		    	})
		    },
		    setTimeVisitor(){
                setTimeout(()=>{ //只在数据为0条时
	                let visitorData = this.$store.state.visitor.visitor;
	                if(visitorData === 0){
	                	this.dataVisitor = [];
	                	this.dataText = '暂无数据';
	                	this.loading = false;
	                }
	            },4000);
		    },
		    sendToExcel(){
		    	//Excel
		    	let tabStr = '<tr><td>ID</td><td>复制时间</td><td>复制微信</td><td>搜索关键词</td><td>搜索引擎</td><td>ip</td><td>省市区</td><td>访问网站</td><td>访问域名</td><td>访问url</td><td>手机型号</td><td>复制方式</td></tr>';

		        for(let i = 0; i < this.dataVisitor.length; i++){
		        	tabStr += '<tr>';
		        	for(let item in this.dataVisitor[i]){
		        		tabStr += `<td>${ this.dataVisitor[i][item] + '\t'}</td>`;
		        	}
		        	tabStr+='</tr>';
		        }
			    let uri = 'data:application/vnd.ms-excel;base64,';
			 
		       //下载的表格模板数据
		        let template = `<html xmlns:o="urn:schemas-microsoft-com:office:office" 
		        xmlns:x="urn:schemas-microsoft-com:office:excel" 
		        xmlns="http://www.w3.org/TR/REC-html40">
		        <head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet>
		        <x:Name></x:Name>
		        <x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet>
		        </x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]-->
		        </head><body><table>${tabStr}</table></body></html>`;

		        let link = document.createElement("a");
		        link.href = uri  + this.setBase64(template);
		        //对下载的文件命名
		        link.download =  "实时访客.xls";
		        document.body.appendChild(link);
		        link.click();
		        document.body.removeChild(link);
		    },
		    sendToCsv(){
		    	  //Csv
		    	  let str = 'ID,复制时间,复制微信,搜索关键词,搜索引擎,ip,省市区,访问网站,访问域名,访问url,手机型号,复制方式\n';

			      for(let i = 0 ; i < this.dataVisitor.length ; i++ ){
			        for(let item in this.dataVisitor[i]){
			            str+=`${ this.dataVisitor[i][item] + '\t'},`;     
			        }
			        str+='\n';
			      }
			      //encodeURIComponent解决中文乱码
			      let uri = 'data:text/csv;charset=utf-8,\ufeff' + encodeURIComponent(str);
			      //通过创建a标签实现
			      let link = document.createElement("a");
			      link.href = uri;
			      //对下载的文件命名
			      link.download =  "实时访客.csv";
			      document.body.appendChild(link);
			      link.click();
			      document.body.removeChild(link);
		    },
		    setBase64(s){
                return window.btoa(unescape(encodeURIComponent(s)));
		    },
		    storeDispath(group,stime,etime,sel,weix,ip){
                this.$store.dispatch("getDataVisitor",{data:group,data1:stime,data2:etime,data3:sel,data4:weix,data5:ip});
		    },
		    storeDispath2(data){
                this.$store.dispatch("getDataOptions",{data:data});
		    },
		    handleSizeChange(val) {
		        console.log(`每页 ${val} 条`);
		    },
		    handleCurrentChange(val) {
		        console.log(`当前页1: ${val}`);
		    }
		}
	}
</script>


<style scoped lang="less">
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
	.m-a{
		margin:15px 0 46px;
	}
	.m-l{
		font-size:14px;
		margin-right: 8px;
		display:inline-block;
	}
	.el-date{
		width:350px;
	}
	.el-option,.el-ip{
		width:140px;
	}
	.el-weixin{
		width:120px;
	}
	.el-button{
		padding:13px;
	}
	.popover-s{
		display:block;
		line-height:26px;
		text-align:left;
		cursor: pointer;
	}
</style>


