import * as req from '@/public/request.js'
import api from '@/config/api.js'
import types from '../types.js'

const state = {
     message:{
     	all:[],
     	sestem:[],
     	update:[]
     }
}
const getters = {
     getDataMessage(state){
       	 return state.message;
     }
}
const mutations = {
   [types.GET_INFO](state,data){
       let sestem = [];
       let update = [];
       for(let i in data){
           if(data[i]['columns'] === '系统公告'){
               sestem.push(data[i]);
           }else if(data[i]['columns'] === '更新日志'){
               update.push(data[i]); 
           }
       }
       state.message.all = data;
       state.message.sestem = sestem;
       state.message.update = update;
   }
}
const actions = {
   getMessage({commit,state}){
       req.get(api.GET_MESSAGE,{message:1}).then(res=>{
           commit(types.GET_INFO,res);
       })
   }
}
export default{
	state,
	getters,
	mutations,
	actions
}


