import * as req from '@/public/request.js'
import api from '@/config/api.js'
import types from '../types.js'

const state = {
    userData:{             //登陆数据-基本资料
    	username:"",
	    password:"",
      username:"",
      male:"",
      phone:"",
      email:"",
      textarea:"",
	    status:""
    }
}

const getters = {
    getUserData(state){
    	return JSON.parse(sessionStorage.getItem("token"));
    }
}

const mutations = {
    [types.GET_STATUS](state,data){
    	  //登陆
        state.userData.username = data.username;
        state.userData.password = data.password;
        state.userData.status = data.status;
        state.userData.nickname = data.nickname;
        state.userData.male = data.male;
        state.userData.phone = data.phone;
        state.userData.email = data.email;
        state.userData.textarea = data.textarea;
    },
}

const actions = {
  getRequest({commit,state}){
      //登陆-基本资料
      req.get(api.GET_LOGIN,{login:1}).then(res => {
           commit(types.GET_STATUS,res[0]);
      })
  }
}

export default {
	state,
	getters,
	mutations,
	actions
}


