<template>
	<div id="main">
		 <!-- 技术支持 -->
		 <el-drawer
		  title=""
		  :visible.sync="drawer"
		  :with-header="false">
		  <div class="copyRight">
		  	  <div class="copy-title">联系我们</div>
			  <div class="copy-content">
			     <ul>
			     	 <li v-for="(item,index) in copyData" :key="item"><a href="javascript:;">{{item}}</a></li>
			     </ul>
			  </div>
		  </div>
		 </el-drawer>
		 <div class="position">
		 	 <el-breadcrumb separator="/">
		 	      <el-breadcrumb-item @click.native="routerPath('/home','vue-main')">数据概览</el-breadcrumb-item>
			 </el-breadcrumb>
			 <div class="refresh" @click="refresh"><span class="el-icon-refresh-right"></span></div>
		 </div>
		 <div class="main-f">
		      <div class="block-s" v-loading="headerStatus">
		      	  <!-- header -->
		      	  <Statis></Statis>
		      </div>
		 </div>
		 <div class="main-f">
		      <div class="block-a">
		      	   <div class="block-l">
		      	       <div class="block-t"><span>访客(复制)来源</span></div>
			           <!-- 搜索引擎 -->
			      	   <Engine v-loading="engineStatus"></Engine>
				   </div>
				   <div class="block-l">
		      	       <div class="block-t"><span>访客(复制)设备</span></div>
			           <!-- 访问设备 -->
			      	   <Phone v-loading="engineStatus1"></Phone>
				   </div>
		      </div>  
		 </div>
		 <div class="main-f">
		 	  <div class="block-c">
		 	  	  <div class="block-l">
		 	  	  	  <div class="block-t"><span>微信复制(全国)分布</span>
		 	  	  	  </div> 
		 	  	  	  <!-- 微信全国分布 -->
		 	  	  	  <Dislist></Dislist>
		 	  	  </div>
		 	  	  <div class="block-l">
		 	  	      <div class="block-t"><span>近七日数据趋势</span>
                        <div>
                      	   <el-select v-model="value" placeholder="选择统计网站" @change="selectOptions">
						      <el-option
						      width="100"
						      v-for="item in options"
						      :key="item.id"
						      :label="item.website"
						      :value="item.id">
						      </el-option>
					       </el-select>
					       <el-button type="primary" size="medium" @click="sendSearchVistor">查询</el-button>
                        </div>
		 	  	      </div> 
		 	  	  	  <!-- 一周复制及访客 -->
		 	  	  	  <Enline v-loading="weekStatus"></Enline>
		 	  	  </div>
		 	  </div>
		 </div>
		 <div class="footer-r" @click="drawer=true"><span>@技术支持</span></div>
	</div>
</template>

<script>
    import api from '@/config/api.js'
    import statis from './charts/statis.vue'
    import engine from './charts/engine.vue'
    import phone from './charts/phone.vue'
    import dislist from './charts/dislist.vue'
    import enline from './charts/enline.vue'
    import { mapState,mapActions } from 'vuex'

	export default{
		data(){
			return{
               drawer: false,
               engineStatus:true,
               engineStatus1:true,
               headerStatus:true,
               weekStatus:true,
               value:'',
               selected:'',
               copyData:[],
               selectWchat:[],
               options:[]
			}
		},
		inject:['reload'],
		created(){
            if(localStorage.getItem("group")){
				 let groups = JSON.parse(localStorage.getItem("group")).data;
				 this.triggerHost(groups); //main(header)
				 this.triggerMain(groups); //main(engine、phone)
				 this.triggerWeek(groups,null,0); //main(week) 后面为状态0为默认，1为站点筛选
				 this.triggerOptions(groups);  //组
			} 
		},
		mounted(){
            this.copyData = [
                "技术支持：1511868047@qq.com",
                "微信：JJtang1314"
            ]
		},
		computed:{
            todayCopy(){
            	return this.$store.state.main.todayCopy;
            }
		},
		watch:{
			'$store.state.main.statusEngine'(){
				this.engineStatus = this.$store.state.main.statusEngine;
			},
			'$store.state.main.statusPhone'(){
				this.engineStatus1 = this.$store.state.main.statusPhone;
			},
			'$store.state.main.statusHeader'(){
				this.headerStatus = this.$store.state.main.statusHeader;
			},
			'$store.state.main.statusWeek'(){
				this.weekStatus = this.$store.state.main.statusWeek;
			},
			'$store.state.visitor.options'(){
				let optionsData = this.$store.state.visitor.options
				this.options = optionsData;
			}
		},
		components:{
			"Statis":statis,
            "Engine":engine,
            "Phone":phone,
            "Dislist":dislist,
            "Enline":enline
		},
		methods:{
            ...mapActions(['getEngineData','getHeaderData','getWeekData','getDataOptions']),
            triggerMain(groups){
            	this.getEngineData(groups); 
            },
            triggerHost(groups){
            	this.getHeaderData(groups);
            },
            triggerWeek(groups,s,n){
            	this.getWeekData({data:groups,select:s,status:n});
            },
            triggerOptions(groups){
            	this.getDataOptions({data:groups});
            },
            selectOptions(e){
            	this.selected = e;
            },
            sendSearchVistor(){
            	let groups = JSON.parse(localStorage.getItem("group")).data;
            	if(this.selected !== ''){
            		console.log("有参数,可以查询");
            	    this.triggerWeek(groups,this.selected,1);
            	}else{
                    console.log("可能没有参数,查询0");
            	}
            },
            refresh(){
                this.reload();
                console.log("刷新")
            }
		}
	}
</script>

<style scoped lang="less">
    #bundle(){
    	.li{
            display: flex;
        	justify-content:space-between;
        	flex-direction: row;
        	align-items: center;
    	}
    	.div{
    		display: flex;
    		justify-content:start;
    		flex-direction:row;
    		align-items:center;
    	}              
    }
	.main-f{
		width:100%;
		font-size: 14px;
	    padding-top: 10px;
	    #bundle.li();
	    .block-s{
	    	width:100%;
	    }
		.block-a,.block-c{
			width:100%;
			margin-top:10px;
			#bundle.div();
			.block-l{
				flex:.5;
				height:300px;
				padding:20px 0;
				.block-t{
					text-align:left;
					line-height:35px;
					margin-bottom: 5px;
					#bundle.li();
					div{
						width:35%;
						text-align:right;
					}
				}
			}
		}
		.block-c{
			.block-l:first-child{
				flex:.25;
			}
			.block-l:last-child{
				flex:.75;
			}
		}
	}
	.footer-r{
		position:fixed;
		left:0;
		bottom:0;
		z-index: 1001;
		width:100%;
		max-width: 220px;
		line-height:30px;
		text-align:center;
		font-size:13px;
		background-color:#ffffff;
		font-weight:normal;
		color:#399cff;
		cursor: pointer;
	}
	.copy-title{
		padding-left: 10px;
		line-height:25px;
		&:after{
		   display:block;
		   position:absolute;
		   left:10px;
		   top:15px;
           content:"";
           width:3px;
           height:25px;
           background-color: #399cff;
	    }
	}
	.copyRight{
        padding:15px;
        text-align:left;
        font-size:14px;
        .copy-content{
        	margin-top: 6px;
            li{
            	line-height:30px;
            	a{
            		text-decoration: none;
                    color:#666666;
            	}
            }
        }
	}
	.position{
		display: flex;
    	justify-content:space-between;
    	flex-direction: row;
    	align-items: center;
    	.refresh{
    		cursor: pointer;
    	}
	}
	.el-main{

	}
</style>


