import axios from 'axios'
import store from '@/store/index.js'
import VueCookies from 'vue-cookies'
import { Loading } from 'element-ui'

/*axios发送请求时的添加请求头*/
//axios.defaults.baseURL = "http://localhost/api"; //开发地址
axios.defaults.baseURL = "http://web-ns.xpfamily.com/api"; //线上地址

/**
* 默认10s请求超时
**/
axios.defaults.timeout = 15000;

/*请求头*/
//axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'; //默认

/*http request 拦截器*/
axios.interceptors.request.use(
    config => {
        /*请求之前*/
        /*判断如果登录了就把token配置到axios的协议中*/
        let token = $cookies.get("token");
        if(token){
            config.headers['Authorization'] = token;
        }
        //console.log("token",token);
        return config
    },
    err => {
        /*请求错误*/
        return Promise.reject(err)
    }
)
/*http response 拦截器*/
axios.interceptors.response.use(
    response => {
        /*相应成功后*/
        //console.log("拦截器",response);
        //console.log("响应后",response)
        return response
    },
    error => {
    	/*相应错误后*/
    	let res = error.response;
    	switch(res.status){
    		case 401:
    		  console.log('token 过期');
    		case 403:
    		  console.log('您没有操作权限');
    		case 500:
              console.log('服务器错误');
    	}
        return Promise.reject(error)
    }
)
/**
* get请求
**/
export function get(url,params){
    return new Promise((resolve,reject) =>{
        axios.get(url,{
        	params:params
        }).then(res => {
        	resolve(res.data);
        }).catch(err => {
        	reject(err.data);
        })
    });
}
/**
* post请求
**/
export function post(url,params,status){
	return new Promise((resolve,reject) => {
		let paramsValue;
		if(status === 1){
	        paramsValue = {
	        	[params.name]:params.data 
	        }; 		
     	}else if(status === 2){
     		paramsValue = params;
     	}else if(status === 3){  //登录带header
            
        }
        axios.post(url,paramsValue,).then(res => {
        	resolve(res.data);
        }).catch(err => {
        	reject(err.data);
        })
	});
}


